<template>
  <div style="position: relative">
    <div class="outer-box" :style="{ width: width + 'px' }">
      {{ getCurrentItem }}
      <img
        class="time-icon"
        @click="ShowSelect"
        src="./resource/down.png"
        alt=""
      />
    </div>
    <div class="SelectBox">
      <div class="display-spa" v-show="isShow">
        <div class="flex-box">
          <div class="item-list">
            <div
              @click="changeItem(index)"
              class="item"
              v-for="(item, index) in List2"
              :key="index"
            >
              {{ item.option }}
            </div>
          </div>
        </div>
        <!-- <div class="submit-buttton" @click="submit">确 认</div> -->
      </div>
      <div class="SelectBox2" v-show="isShow2">
        <div class="flex-box">
          <div class="item-list2">
            <div
              @click="changeItem(index + 3)"
              class="item2"
              v-for="(item, index) in List3"
              :key="index"
            >
              {{ item.option }}
            </div>
          </div>
        </div>
        <!-- <div class="submit-buttton" @click="submit">确 认</div> -->
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import { reactive, toRaw } from "@vue/reactivity";
import { computed } from "@vue/runtime-core";
import { useStore } from "vuex";
import { ref } from "@vue/reactivity";
export default {
  name: "SelectInput",
  props: {
    value: String || Number,
    width: String,
    List: Array,
    title: String,
  },
  mounted() {
    let box = Array.from(document.getElementsByClassName("SelectBox"));
    window.addEventListener("mousedown", (e) => {
      // 获取被点击的元素
      const clickedEl = e.target;
      // `el` 是你正在检测外部点击的元素
      if (
        box.some((item) => {
          return item.contains(clickedEl);
        })
      ) {
        // 单击“el”内部
      } else {
        // 在`el`之外点击
        this.isShow = false;
        this.isShow2 = false;
      }
    });
  },
  data() {
    return {
      curIndex: 0,
      isShow: false,
      isShow2: false,
      isFirst: true,
      List2: [],
      List3: [],
    };
  },
  computed: {
    getCurrentItem() {
      console.log(this.List);
      this.List2 = this.List.slice(0, 3);
      this.List3 = this.List.slice(3);
      console.log(this.List2);
      console.log(this.List3);
      let value = this.$props?.value;
      console.log(value);
      if (value === "" && this.isFirst) {
        return this.$props.title || "";
      }
      if (value) {
        return this.getCurrentValue();
      }
      // return this.List[this.curIndex]?.option || "";
      return this.title;
    },
  },
  methods: {
    submit() {
      this.isShow = false;
    },
    ShowSelect() {
      this.isShow = !this.isShow;
    },
    changeItem(index) {
      console.log(index);
      if (index == 2) {
        this.isShow2 = true;
      } else {
        this.isFirst = false;
        this.curIndex = index;
        this.$emit("sendValue", this.List[index].value);
      }
    },
    getCurrentValue() {
      let valueList = this.List.map((item) => {
        return item.value;
      });
      console.log(valueList);
      const index = valueList.indexOf(this.value);
      console.log(index);
      if (index != -1) {
        return this.List[index].option.slice(0, 8);
      }
      return this.title;
    },
  },
};
</script>

