<template>
  <field-structure :fieldTitle="fieldTitle">
    <div class="switch-box">
      <div class="mo" @click="switchS" :class="globalValue ? 'onmo' : ''">
        <div class="inn" :class="globalValue ? 'on' : ''"></div>
      </div>
    </div>
  </field-structure>
</template>

<script>
import { computed, reactive, ref, toRaw } from "@vue/runtime-core";
import { useStore } from "vuex";
import FieldStructure from "./FieldStructure.vue";
export default {
  name: "SimpleText",
  components: {
    FieldStructure,
  },
  props: {
    field: Object,
  },
  setup(props, context) {
    const store = useStore();
    const valueLength = ref(0);
    let { charLimit, fieldTitle, fieldKey, ins } = toRaw(props.field);
    if (!charLimit) {
      charLimit = 20;
    }
    const globalValue = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        if (!value) {
          store.commit("productPage/setPayload", {
            [fieldKey]: {
              value: true,
            },
          });
          return true;
        }
        return value.value;
      },
      set(val) {
        updateKey(val);
      },
    });
    const updateKey = (val) => {
      store.commit("productPage/setPayload", {
        [fieldKey]: {
          value: val,
        },
      });
    };
    const updateLength = function (l) {
      valueLength.value = l;
    };
    const switchValue = reactive({ switchValue: false });

    return {
      globalValue,
      fieldTitle,
      fieldKey,
      valueLength,
      charLimit,
      ins,
      switchValue,
    };
  },
  methods: {
    switchS() {
      this.globalValue = !this.globalValue;
    },
  },
};
</script>

<style lang="less" scoped>
.mo {
  width: 40px;
  height: 22px;
  border-radius: 20px;
  background-color: #bfbfbf;
}
.onmo {
  background-color: #93d500;
}
.inn {
  width: 18px;
  height: 18px;
  border-radius: 100px;
  background-color: #fff;
  transform: translate(2px, 2px);
  transition: transform 0.2s ease-out;
}
.on {
  transform: translate(20px, 2px);
}
</style>
