<template>
  <field-structure :fieldTitle="fieldTitle">
    <div class="multi-selects">
      <div class="selects">
        <div
          class="selecti"
          v-for="(v, i) in typess"
          :key="i"
          @click="select(v)"
        >
          <div
            class="selectC right15"
            :class="v.selected ? 'selected' : ''"
          ></div>
          <div class="select-name">{{ v.name }}</div>
        </div>
      </div>
      <div class="ins">*可选择多项</div>
    </div>
  </field-structure>
</template>

<script>
import { computed, reactive, ref, toRaw } from "@vue/runtime-core";
import { useStore } from "vuex";
import FieldStructure from "./FieldStructure.vue";
export default {
  name: "SimpleText",
  components: {
    FieldStructure,
  },
  props: {
    field: Object,
  },
  setup(props, context) {
    const store = useStore();
    const valueLength = ref(0);
    let { fieldTitle, fieldKey, types } = toRaw(props.field);
    const typess = reactive(
      types.map((e, i) => ({
        name: e,
        ind: i,
        selected: false,
      }))
    );
    // console.log(typess)
    const select = (v) => {
      // console.log(v)
      v.selected = !v.selected;
    };
    return {
      fieldTitle,
      fieldKey,
      typess,
      select,
    };
  },
};
</script>

