  setup(props, context) {
    // console.log(props)
    const isShow = ref(false)
    const isShow2 = ref(false)
    const isShow3 = ref(false)
    const showTxar2 = ref(false)
    const showTxar3 = ref(false)
    const arr = [
      { name: '天安·意外险·(低费率)每人身故伤残限额5万档' },
      { name: '天安·意外险·(一般保障)每人身故伤残限额10万档' },
      { name: '天安·意外险·(一般保障)每人身故伤残限额20万档' },
      { name: '天安·意外险·(一般保障)每人身故伤残限额30万档' },
      { name: '天安·意外险·(救援责任)每人身故伤残限额10万档' },
      { name: '天安·意外险·(救援责任)每人身故伤残限额20万档' },
      { name: '天安·意外险·(救援责任)每人身故伤残限额30万档' }
    ]
    const getCurrentItem = ref('请选择保险购买方式')
    const getCurrentItem2 = ref('请选择购买的保险类型')
    const txpl =
      '例：您选择投保由平安财险承保的组织者保险，基础费率2元/天。每次事故没人赔偿限额10玩（其中医药费用赔偿限额为1完）；没人财产损失赔偿限额1000元；每次及累计赔偿限额100万，具体保障范围请详询保险公司或查看保单条款'
    const store = useStore()
    const { charLimit, fieldTitle, style, required, inputid, first, second } =
      toRaw(props.field)

    const selected = reactive({
      one: false,
      two: false
    })
    watchEffect(() => {
      if (
        store.state.productPage.payload.accident_insurance_self_details?.value
      ) {
        isShow3.value = true
        getCurrentItem2.value =
          store.state.productPage.payload.accident_insurance_self_details.value
        getCurrentItem.value = '购买营探合作的第三方保险'
      }
    })
    // store.commit("productPage/setPayload", {
    //   [first]: {
    //     value: false,
    //   },
    // });
    // store.commit("productPage/setPayload", {
    //   [second]: {
    //     value: "",
    //   },
    // });
    // store.commit("productPage/setPayload", {
    //   ["accident_insurance_self_details"]: {
    //     value: "天安·意外险·(低费率)每人身故伤残限额5万档",
    //   },
    // });
    const txarvalue = computed({
      get() {
        let value = store.state.productPage.payload[second]
        if (!value) {
          // store.commit("productPage/setPayload", {
          //   [second]: {
          //     value: "",
          //   },
          // });
          return txpl
        }
        return value.value
      },
      set(val) {
        updateDetail(val)
      }
    })
    const showTxar = computed({
      get() {
        let value = store.state.productPage.payload[first]
        if (!value) {
          // store.commit("productPage/setPayload", {
          //   [first]: {
          //     value: false,
          //   },
          // });
          return false
        }
        return value.value
      },
      set(val) {
        updateDetail(val)
      }
    })
    const show = e => {
      // console.log(e)
      updateType(e)
    }
    const show2 = item => {
      getCurrentItem2.value = item.name
      store.commit('productPage/setPayload', {
        ['accident_insurance_self_details']: {
          value: item.name
        }
      })
      isShow2.value = false
      showTxar3.value = true
    }
    const ShowSelect = () => {
      isShow.value = true
    }
    const cancel = () => {
      showTxar2.value = false
    }
    const cancel2 = () => {
      showTxar3.value = false
    }
    const ShowSelect2 = () => {
      isShow2.value = true
    }
    const updateType = e => {
      if (e == 2) {
        getCurrentItem.value = '自行购买其他保险'
        isShow3.value = false
        showTxar2.value = true
        store.commit('productPage/setPayload', {
          ['accident_insurance_self_details']: {
            value: ''
          }
        })
      }
      if (e == 1) {
        getCurrentItem.value = '购买营探合作的第三方保险'
        isShow3.value = true
        store.commit('productPage/setPayload', {
          ['accident_insurance_self_details']: undefined
        })
