<template>
  <div class="label">支持语言:</div>
  <div class="content">
    <Select :options="options" @valueChanged="change" :value="globalValue" />
  </div>
  <div class="clear"></div>
</template>

<script>
import { useStore } from "vuex";
import { computed } from "@vue/runtime-core";
import Select from "@/pages/internal/views/providerTools/activityCreateForm/inputs/Select.vue";

export default {
  name: "LanguageSelect",
  setup(props, context) {
    const store = useStore();
    store.commit("productPage/setPayload", { language: { value: ["中文"] } });
    const globalValue = computed({
      get() {
        let entity = store.state.productPage.payload["language"];
        if (entity) return entity.value;
        return ["中文", "普通话"];
      },
      set(val) {
        let payload = {};
        payload["language"] = { value: val };
        store.commit("productPage/setPayload", payload);
      },
    });
    return {
      globalValue,
    };
  },
  data() {
    return {
      options: [
        { name: "中文(必选)", value: "中文" },
        { name: "英文", value: "英文" },
        { name: "俄语", value: "俄语" },
        { name: "日语", value: "日语" },
        { name: "西语", value: "西语" },
        { name: "泰语", value: "泰语" },
        { name: "韩语", value: "韩语" },
        { name: "普通话(必选)", value: "普通话" },
        { name: "上海话", value: "上海话" },
        { name: "粤语", value: "粤语" },
        { name: "闽南语", value: "闽南语" },
        { name: "陕西话", value: "陕西话" },
        { name: "蒙语", value: "蒙语" },
      ],
    };
  },
  methods: {
    change(val) {
      this.globalValue = val;
    },
  },
  components: {
    Select,
  },
};
</script>

<style scoped></style>
