<template>
  <field-structure :fieldTitle="fieldTitle">
    <div class="single-select">
      <!-- <select name="" id="" class="basic-input" v-model="globalValue">
                <option value="0" selected>请选择</option>
                <option :value="item.id" :key="index" v-for="(item,index) in showList">{{item.name}}</option>
            </select> -->
      <SelectInput
        :List="selectList"
        :value="globalValue"
        @sendValue="changeType"
        title="请选择"
      ></SelectInput>
    </div>
    <!-- <textarea
      v-show="globalValue == 1"
      v-model="schemeDetail"
      class="scheme-detail-input"
      placeholder="请填写具体退改方案"
    /> -->
  </field-structure>
</template>

<script>
import { computed, ref, toRaw } from "@vue/runtime-core";
import { useStore } from "vuex";
import FieldStructure from "./FieldStructure.vue";
import SelectInput from "./SelectInput.vue";
import SelectInput2 from "./SelectInput2.vue";
import { $getRefundpolicyAll } from "@/pages/internal/network/providerTools/provider.js";
export default {
  name: "SimpleText",
  components: {
    FieldStructure,
    SelectInput,
  },
  props: {
    field: Object,
  },
  data() {
    return {
      showList: [],
    };
  },
  computed: {
    selectList() {
      return this.showList.map((item) => {
        return {
          value: item.id,
          option: item.name,
        };
      });
    },
  },
  mounted() {
    const that = this;
    async function getRefundpolicyAll() {
      const res = await $getRefundpolicyAll({});
      console.log("refund", res);
      if (res.Code == 200) {
        that.showList = res.details;
      }
    }
    getRefundpolicyAll();
  },
  setup(props, context) {
    const store = useStore();
    const valueLength = ref(0);
    let { charLimit, fieldTitle, fieldKey, ins } = toRaw(props.field);
    if (!charLimit) {
      charLimit = 20;
    }
    const schemeDetail = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        console.log(value);
        if (!value) {
          return "";
        }
        return value.content || "";
      },
      set(val) {
        let value = store.state.productPage.payload[fieldKey];
        store.commit("productPage/setPayload", {
          [fieldKey]: {
            ...value,
            content: val,
          },
        });
        store.commit("productPage/setPayload", {
          [`${fieldKey}_id`]: {
            ...value,
            content: val,
          },
        });
      },
    });
    const globalValue = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        console.log("退改方案____________________________________________");
        if (!value) {
          return 0;
        }
        return value.value;
      },
      set(val) {
        store.commit("productPage/setPayload", {
          [fieldKey]: {
            value: Number(val),
          },
        });
        store.commit("productPage/setPayload", {
          [`${fieldKey}_id`]: {
            value: Number(val),
          },
        });
      },
    });
    const changeType = (e) => {
      globalValue._setter(e);
    };
    return {
      globalValue,
      fieldTitle,
      fieldKey,
      valueLength,
      charLimit,
      ins,
      schemeDetail,
      changeType,
    };
  },
};
</script>

