<template>
  <field-structure :fieldTitle="field.fieldTitle">
    <div class="date-span" ref="box">
      <div style="display: flex">
        <div class="display-spa" @click="switchPicker">
          <div class="start">{{ startDate }}</div>

          <div class="icon"></div>
        </div>
        <DayTimeSelect
          :value="field.fieldKey"
          @sendValue="getMessage"
        ></DayTimeSelect>
      </div>
      <div class="two-pickers" v-show="showPickers.value">
        <DatePicker
          :fieldKey="fieldKey"
          :ifSingle="true"
          @close="showPickers.value = false"
        />
      </div>
    </div>
  </field-structure>
</template>

<script>
import FieldStructure from "./FieldStructure.vue";
import DatePicker from "./DatePicker.vue";
import { reactive, toRaw } from "@vue/reactivity";
import { computed } from "@vue/runtime-core";
import { useStore } from "vuex";
import { ref } from "@vue/reactivity";
import DayTimeSelect from "./DayTimeSelect.vue";
import { onMounted } from "@vue/runtime-core";
export default {
  components: {
    FieldStructure,
    DatePicker,
    DayTimeSelect,
  },
  props: {
    field: Object,
  },

  setup(props, context) {
    // console.log(new Date().toISOString())
    const store = useStore();
    const { fieldTitle, fieldKey, placeholder } = toRaw(props.field);
    let showPickers = reactive({ value: false });
    const startDate = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        if (value) {
          value = value.value;
        }
        if (!value) {
          return `   ${placeholder}`;
        }
        let date = new Date(value);
        let res =
          date.getFullYear() +
          "/" +
          Number(date.toISOString().substr(5, 2)) +
          "/" +
          date.getDate();
        // console.log(res)
        return res;
      },
    });
    let box = ref(null);
    //挂载
    const switchPicker = function () {
      console.log("switch   a");
      let statue = showPickers.value;
      console.log(statue);
      showPickers.value = !statue;
    };
    const closePicker = function () {
      showPickers.value = false;
    };
    onMounted(() => {
      window.addEventListener("mousedown", (e) => {
        // 获取被点击的元素
        const clickedEl = e.target;

        // `el` 是你正在检测外部点击的元素
        if (box.value?.contains(clickedEl)) {
          // 单击“el”内部
        } else {
          // 在`el`之外点击
          closePicker();
        }
      });
    });

    return {
      fieldTitle,
      fieldKey,
      startDate,
      box,
      showPickers,
      switchPicker,
    };
  },
  data() {
    return {};
  },
  methods: {},
  created() {},
};
</script>

