<template>
  <div
    class="out dir-date-picker2"
    @click="ifShow = !ifShow"
    v-clickoutside:dir-date-picker2="close"
  >
    {{ childvalue }}
    <div class="calender"></div>
    <div class="picker" v-show="ifShow" @click.stop="">
      <div class="date-picker">
        <div class="year-month">
          <div class="start-date">
            <div class="year-" @click="minusYear(-1)">&lt;&lt;</div>
            <div class="month-" @click="minusMonth(-1)">&lt;</div>
            <div class="year-month-dis">
              {{ startYear }}年{{ startMonth }}月
            </div>
            <div class="month-add" @click="minusMonth(1)">&gt;</div>
            <div class="year-add" @click="minusYear(1)">&gt;&gt;</div>
          </div>
        </div>
        <div class="pickers">
          <div class="left-pickers">
            <div class="weekdays">
              <div
                class="weekday"
                v-for="(v, indd) in ['日', '一', '二', '三', '四', '五', '六']"
                :key="indd"
              >
                {{ v }}
              </div>
            </div>
            <div class="days-date">
              <div
                class="date-day"
                :class="v.currentD ? 'currentD' : v.currentM ? 'currentM' : ''"
                v-for="(v, indd) in startDates"
                :key="indd"
                @click="selectDate(v)"
              >
                {{ v.ri }}
              </div>
            </div>
          </div>
        </div>
        <div class="bottom">
          <div class="today" @click="toToday">今天</div>
          <div class="close" @click="ifShow = !ifShow">确认</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import FieldStructure from "./FieldStructure.vue";
import { onMounted } from "@vue/runtime-core";

const getYesterday = (date) => {
  let time = date.getTime();
  let yesterday = new Date(time - 24 * 60 * 60 * 1000);
  return yesterday;
};
const getTomorrow = (date) => {
  let time = date.getTime();
  let tomorrow = new Date(time + 24 * 60 * 60 * 1000);
  return tomorrow;
};
const getDate42 = (date) => {
  return [...getBeforeDates(date), date, ...getAfterDates(date)];
};
const getBeforeDates = (date) => {
  let dates = [];
  let day = date.getDay();
  let datenumber = 14 + day;
  // console.log(datenumber)

  for (let i = 0; i < datenumber; i++) {
    date = getYesterday(date);
    dates.unshift(date);
  }
  return dates;
};
const getAfterDates = (date) => {
  let dates = [];
  let day = date.getDay();
  let datenumber = 21 + (6 - day);
  // console.log(datenumber)

  for (let i = 0; i < datenumber; i++) {
    date = getTomorrow(date);
    dates.push(date);
  }
  return dates;
};
export default {
  components: {
    FieldStructure,
  },
  props: {
    value: String,
    num: Number,
  },

  data() {
    return {
      childvalue: this.value,
      showPickers: false,
      dates: (function () {
        let a = [];
        for (let i = 0; i < 42; i++) {
          if (i < 10) {
            a.push("0" + i);
          } else {
            a.push(i);
          }
        }
        return a;
      })(),
      startYear: "",
      endYear: "",
      startMonth: "",
      endMonth: "",
      startDay: "",
      endDay: "",
      startDate: "",
      endDate: "",
      startDates: [],
      endDates: [],
      startDateO: null,
      endDateO: null,
      ifShow: false,
    };
  },
  methods: {
    close() {
      this.ifShow = false;
    },
    toToday() {
      this.setStartState(new Date());
    },
    selectDate(e) {
      // console.log(e)
      this.setStartState(e.dateo);
    },
    setStartState(date) {
      let a = date;
      let currentMonth = a.getMonth();

      let dates = getDate42(a);
      this.startDates = dates.map((e) => ({
        dateo: e,
        date: e.toISOString(),
        ri: e.getDate(),
        currentD: e == a ? true : false,
        currentM: e.getMonth() == currentMonth ? true : false,
      }));
      this.startYear = date.getFullYear();

      this.startMonth = Number(date.toISOString().substr(5, 2));

      this.startDay = date.getDate();
      this.startDateO = a;

      let tmp =
        this.startYear +
        "-" +
        (this.startMonth >= 10 ? "" : "0") +
        this.startMonth +
        "-" +
        (this.startDay >= 10 ? "" : "0") +
        this.startDay;
      this.childvalue = tmp;
    },
    minusYear(yearNumber) {
      // console.log(yearNumber)
      let today = this.startDateO;
      let toyear = today.getFullYear();
      let lastyear = toyear + yearNumber;
      // console.log(lastyear)
      let lastyeartody = new Date(today.setFullYear(lastyear));
      // console.log(lastyeartody)
      this.setStartState(lastyeartody);
    },
    minusMonth(monthnumber) {
      // console.log(monthnumber)
      let today = this.startDateO;
      let toMonth = today.getMonth();
      let lastMongth = toMonth + monthnumber;
      // console.log(lastyear)
      let lastMonthtody = new Date(today.setMonth(lastMongth));
      // console.log(lastyeartody)
      this.setStartState(lastMonthtody);
    },
  },
  created() {
    if (this.value && this.value !== "") {
      // console.log(new Date(this.value))
      this.setStartState(new Date(this.value));
      return;
    }
    // console.log(a)
    // console.log(a.getMonth())
    this.setStartState(new Date());
    // console.log(this.$props.fieldKey)
  },
  watch: {
    childvalue(val) {
      this.$emit("valueChanged", val, this.num);
    },
    value(val) {
      this.childvalue = val;
      if (val !== "")
        // console.log(this.value,new Date(this.value))
        this.setStartState(new Date(this.value));
    },
  },
};
</script>

