<template>
  <field-structure :fieldTitle="fieldTitle" :required="required">
    <div class="inss">
      <div class="camp_p selectables">
        <div
          class="selectC right15"
          :class="limited ? 'selected' : ''"
          @click="show(2)"
        ></div>
        <div class="genr">设置</div>
        <div class="set-mini-right">
          <div class="numer">
            低于
            <input
              type="number"
              placeholder="整数"
              class="basic-input"
              v-model="limitSize"
            />
            人不成团
          </div>
          <div class="note">*不成团将全额退款，互不承担责任</div>
        </div>
      </div>
      <div class="custo selectables">
        <div
          class="selectC right15"
          :class="!limited ? 'selected' : ''"
          @click="show(1)"
        ></div>
        <div class="genr right15">不设限制</div>
        <div class="note">*报名审核通过即成团</div>
      </div>
    </div>
  </field-structure>
</template>

<script>
import { computed, reactive, ref, toRaw } from "@vue/runtime-core";
import { useStore } from "vuex";
import FieldStructure from "./FieldStructure.vue";
export default {
  ewsdddddname: "InsSelect",
  components: {
    FieldStructure,
  },
  props: {
    field: Object,
  },
  mounted() {},
  setup(props, context) {
    // console.log(props)
    const store = useStore();
    const {
      charLimit,
      fieldTitle,
      fieldKey,
      style,
      required,
      inputid,
      first,
      second,
    } = toRaw(props.field);
    const limitSize = computed({
      get() {
        let value = store.state.productPage.payload[second];
        if (!value) return 0;
        return value.value;
      },
      set(val) {
        // 保证人数不小于0
        if (val < 0) {
          val = 0;
        }
        updateDetail(val);
      },
    });
    const limited = computed({
      get() {
        let value = store.state.productPage.payload[first];
        if (!value) {
          store.commit("productPage/setPayload", {
            [first]: {
              value: false,
            },
          });
          store.commit("productPage/setPayload", {
            [second]: {
              // value:'无限制'
              value: 0,
            },
          });
          return false;
        }
        return value.value;
      },
      set(val) {
        updateDetail(val);
      },
    });
    const updateDetail = (e) => {
      store.commit("productPage/setPayload", {
        [first]: {
          value: true,
        },
      });
      store.commit("productPage/setPayload", {
        [second]: {
          value: e,
        },
      });
    };

    const show = (e) => {
      console.log("show", e);
      updateType(e);
    };
    const updateType = (e) => {
      // console.log(Boolean(e-1))
      store.commit("productPage/setPayload", {
        [first]: {
          value: Boolean(e - 1),
        },
      });
    };
    return {
      charLimit,
      fieldTitle,
      fieldKey,
      style,
      required,

      show,
      inputid,
      updateDetail,
      limited,
      limitSize,
      first,
      second,
    };
  },
  methods: {},
};
</script>

