<template>
  <field-structure :fieldTitle="field.fieldTitle">
    <div class="date-span" ref="box">
      <div class="display-spa" @click="switchPicker(0)">
        <div class="start">{{ startDate }}</div>
        <div class="icon"></div>
      </div>
      <img class="right" src="./resource/right.png" alt="" />
      <div class="display-spa" @click="switchPicker(1)">
        <div class="end">{{ endDate }}</div>
        <div class="icon"></div>
      </div>
      <div class="two-pickers" v-show="showPickers[0].value">
        <DatePicker :fieldKey="fieldKey[0]" />
      </div>
      <div class="two-pickers2" v-show="showPickers[1].value">
        <DatePicker :fieldKey="fieldKey[1]" />
      </div>
      <slot></slot>
    </div>
  </field-structure>
</template>

<script>
import FieldStructure from "./FieldStructure.vue";
import DatePicker from "./DatePicker.vue";
import { reactive, toRaw } from "@vue/reactivity";
import { computed } from "@vue/runtime-core";
import { useStore } from "vuex";
import { ref } from "@vue/reactivity";

import { onMounted } from "@vue/runtime-core";
export default {
  components: {
    FieldStructure,
    DatePicker,
  },
  props: {
    field: Object,
  },
  setup(props, context) {
    // console.log(new Date().toISOString())
    const store = useStore();
    const { fieldTitle, fieldKey } = toRaw(props.field);
    // console.log(fieldTitle,fieldKey)

    const startDate = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey[0]];
        if (value) {
          value = value.value;
        }
        if (!value) {
          return "开始日期";
        }
        let date = new Date(value);
        let res =
          date.getFullYear() +
          "/" +
          Number(date.toISOString().substr(5, 2)) +
          "/" +
          date.getDate();
        // console.log(res)
        return res;
      },
    });
    const endDate = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey[1]];
        if (value) {
          value = value.value;
        }
        if (!value) {
          return "结束日期";
        }
        let date = new Date(value);
        let res =
          date.getFullYear() +
          "/" +
          Number(date.toISOString().substr(5, 2)) +
          "/" +
          date.getDate();
        // console.log(res)
        return res;
      },
    });
    let box = ref(null);
    let showPickers = reactive([{ value: false }, { value: false }]);
    //挂载
    const switchPicker = function (index) {
      console.log(showPickers);
      let statue = showPickers[index].value;
      console.log(statue);
      showPickers[index].value = !statue;
    };
    const closePicker = function () {
      showPickers.forEach((item) => {
        item.value = false;
      });
    };
    onMounted(() => {
      window.addEventListener("mousedown", (e) => {
        // 获取被点击的元素
        const clickedEl = e.target;

        // `el` 是你正在检测外部点击的元素
        if (box.value?.contains(clickedEl)) {
          // 单击“el”内部
        } else {
          // 在`el`之外点击
          closePicker();
        }
      });
    });

    return {
      fieldTitle,
      fieldKey,
      startDate,
      endDate,
      box,
      showPickers,
      switchPicker,
    };
  },
  data() {
    return {};
  },
  methods: {},
  created() {},
};
</script>

