<template>
  <field-structure :fieldTitle="fieldTitle">
    <div class="text-area-with-note">
      <div class="note">
        {{ note }}
      </div>
      <div class="input-flex">
        <textarea
          :id="fieldKey"
          class="ele-input"
          name="te"
          cols="27"
          rows="10"
          v-model="globalValue"
          placeholder="请输入"
          :style="style"
        >
        </textarea>
        <div class="char-limit">
          <span>{{ valueLength }}</span
          >/{{ charLimit }}
        </div>
      </div>
    </div>
  </field-structure>
</template>

<script>
import { computed, ref, toRaw } from "@vue/runtime-core";
import { useStore } from "vuex";
import FieldStructure from "./FieldStructure.vue";
export default {
  name: "SimpleText",
  components: {
    FieldStructure,
  },
  props: {
    field: Object,
  },
  setup(props, context) {
    // console.log(props)
    const store = useStore();
    const valueLength = ref(0);
    const { charLimit, fieldTitle, fieldKey, style, note } = toRaw(props.field);
    // console.log('style: ',style)
    const globalValue = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        if (!value) {
          return "";
        }
        return value.value;
      },
      set(val) {
        updateKey(val);
      },
    });
    const updateKey = (val) => {
      if (val.length > charLimit) {
        // console.log('chaole')
        val = val.substring(0, charLimit);
      }
      store.commit("productPage/setPayload", {
        [fieldKey]: {
          value: val,
        },
      });

      updateLength(val.length);
    };
    const updateLength = function (l) {
      valueLength.value = l;
    };
    return {
      globalValue,
      fieldTitle,
      fieldKey,
      valueLength,
      charLimit,
      note,
      style,
    };
  },
};
</script>

