<template>
  <field-structure :fieldTitle="fieldTitle">
    <div class="emer-con">
      <div class="note">
        *请填写主理人团队紧急联系方式，包括时间和具体活动地点
      </div>
      <div class="contacts">
        <div class="contact" v-for="(v, i) in contacts" :key="i">
          <div class="uper">
            <div class="name">
              <input
                v-model="v.name"
                type="text"
                class="basic-input"
                placeholder="姓名"
                style="width: 150px; margin-right: 10px"
              />
            </div>
            <div class="title-con">
              <input
                v-model="v.title"
                type="text"
                class="basic-input"
                placeholder="团队职务"
                style="width: 150px; margin-right: 10px"
              />
            </div>
            <div class="phone">
              <input
                v-model="v.phone"
                type="text"
                class="basic-input"
                placeholder="电话号码"
              />
            </div>
          </div>
          <img
            v-if="i != 0"
            @click="deleteItem(i)"
            src="../Modules/3_Schedule/resource/delete.png"
            alt=""
          />
          <div class="ps">
            <textarea
              v-model="v.detail"
              class="basic-input"
              name=""
              id=""
              cols="30"
              rows="10"
              placeholder="有时间要求或者其他闲置，请补充在此（选填）"
            ></textarea>
          </div>
        </div>
      </div>

      <div class="adcon" @click="addcon">添加联系人</div>
    </div>
  </field-structure>
</template>

<script>
import { computed, reactive, ref, toRaw } from "@vue/runtime-core";
import { useStore } from "vuex";
import FieldStructure from "./FieldStructure.vue";

export default {
  name: "SimpleText",
  components: {
    FieldStructure,
  },
  props: {
    field: Object,
  },
  setup(props, context) {
    // console.log(props)
    const store = useStore();
    const valueLength = ref(0);
    const { charLimit, fieldTitle, fieldKey, style } = toRaw(props.field);

    // console.log('style: ',style)
    store.commit("productPage/setPayload", {
      [fieldKey]: {
        value: [
          {
            name: "",
            title: "",
            phone: "",
            detail: "",
          },
        ],
      },
    });
    const deleteItem = (index) => {
      let contactsList = contacts.value;
      contactsList.splice(index, 1);
      store.commit("productPage/setPayload", {
        [fieldKey]: {
          value: contactsList,
        },
      });
    };
    const contacts = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        if (!value) {
          store.commit("productPage/setPayload", {
            [fieldKey]: {
              value: [
                {
                  name: "",
                  title: "",
                  phone: "",
                  detail: "",
                },
              ],
            },
          });
        }
        value = store.state.productPage.payload[fieldKey];
        return value.value;
      },
      set(val) {},
    });

    const addcon = () => {
      let value = toRaw(store.state.productPage.payload[fieldKey]);
      if (!value) {
        value = { value: [] };
      }
      value = value.value;
      value.push({
        name: "",
        title: "",
        phone: "",
        detail: "",
      });
      store.commit("productPage/setPayload", {
        [fieldKey]: {
          value,
        },
      });
    };
    return {
      fieldTitle,
      fieldKey,
      valueLength,
      charLimit,
      deleteItem,
      style,
      contacts,
      addcon,
    };
  },
};
</script>

