<template>
  <field-structure :fieldTitle="fieldTitle" :required="required">
    <div class="inss">
      <div class="camp_p selectables">
        <div
          class="selectC right15"
          :class="!showTxar ? 'selected' : ''"
          @click="show(1)"
        ></div>
        <div class="genr">购买第三方保险</div>
      </div>
      <div class="custo selectables">
        <div
          class="selectC right15"
          :class="showTxar ? 'selected' : ''"
          @click="show(2)"
        ></div>
        <div class="genr right15">自行购买</div>
        <!-- <select name="" id="" class="basic-input right15">
                    <option value="">请填写保险公司</option>
                </select>
                <select name="" id="" class="basic-input right15">
                    <option value="">请选择保额</option>
                </select>
                <div class="up">
                    <div class="inp" @click="choosefile">
                    
                        <div class="uppic"  >
                        </div>
                        上传保险协议
                        <input v-show='false' type="file" :id="inputid">
                    </div>
                    <div class="so">支持扩展名： .rar .zip .doc .docx .pdf .jpg...</div>
                </div> -->
      </div>
      <div class="custom-des" v-show="showTxar">
        <div class="ins">请您描述自购的组织责任险保险名称及方案</div>
        <textarea
          name=""
          id=""
          cols="30"
          rows="10"
          :placeholder="txpl"
          v-model="txarvalue"
          class="basic-input insu-area"
        ></textarea>
      </div>
    </div>
  </field-structure>
</template>

<script>
import { computed, reactive, ref, toRaw } from "@vue/runtime-core";
import { useStore } from "vuex";
import FieldStructure from "./FieldStructure.vue";
export default {
  name: "InsSelect",
  components: {
    FieldStructure,
  },
  props: {
    field: Object,
  },
  setup(props, context) {
    // console.log(props)
    const txpl =
      "例：您选择投保由平安财险承保的组织者保险，基础费率2元/天。每次事故没人赔偿限额10玩（其中医药费用赔偿限额为1完）；没人财产损失赔偿限额1000元；每次及累计赔偿限额100万，具体保障范围请详询保险公司或查看保单条款";
    const store = useStore();
    const { charLimit, fieldTitle, style, required, inputid, first, second } =
      toRaw(props.field);

    const selected = reactive({
      one: false,
      two: false,
    });
    store.commit("productPage/setPayload", {
      [first]: {
        value: false,
      },
    });
    store.commit("productPage/setPayload", {
      [second]: {
        value: "",
      },
    });
    const txarvalue = computed({
      get() {
        let value = store.state.productPage.payload[second];

        if (!value) {
          store.commit("productPage/setPayload", {
            [second]: {
              value: "",
            },
          });
          return txpl;
        }
        return value.value;
      },
      set(val) {
        updateDetail(val);
      },
    });
    const showTxar = computed({
      get() {
        let value = store.state.productPage.payload[first];
        if (!value) {
          store.commit("productPage/setPayload", {
            [first]: {
              value: false,
            },
          });
          return false;
        }
        return value.value;
      },
      set(val) {
        updateDetail(val);
      },
    });
    const show = (e) => {
      // console.log(e)
      updateType(e);
    };
    const updateType = (e) => {
      // console.log(Boolean(e-1))
      store.commit("productPage/setPayload", {
        [first]: {
          value: Boolean(e - 1),
        },
      });
    };
    const updateDetail = (e) => {
      store.commit("productPage/setPayload", {
        [second]: {
          value: e,
        },
      });
    };
    const choosefile = (e) => {
      // console.log(e)
      // console.log(this)
      document.getElementById(inputid).click();
    };
    return {
      charLimit,
      fieldTitle,
      style,
      required,
      selected,
      show,
      inputid,
      choosefile,
      showTxar,
      txpl,
      txarvalue,
      first,
      second,
    };
  },
  methods: {},
};
</script>

