<script>
/* eslint-disable */
import { reactive, toRaw } from "@vue/reactivity";
import FieldStructure from "./FieldStructure.vue";
import { computed, onMounted } from "@vue/runtime-core";
import store from "@/pages/internal/store/store";
import { $getEnvInfo } from "@/pages/internal/network/providerTools/activity";
export default {
  name: "SimpleText",
  components: {
    FieldStructure,
  },
  props: {
    field: Object,
  },
  setup(props) {
    let { fieldKey } = toRaw(props.field);
    let selectList = reactive([
      {
        name: "体育",
        id: 1,
        selected: false,
      },
      {
        name: "机械",
        id: 3,
        selected: false,
      },
      {
        name: "手工",
        id: 4,
        selected: false,
      },
      {
        name: "地理",
        id: 5,
        selected: false,
      },
      {
        name: "医疗",
        id: 6,
        selected: false,
      },
      {
