      },
      {
        name: "其他",
        id: 15,
        selected: false,
      },
      {
        name: "亲子",
        id: 17,
        selected: false,
      },
    ]);
    let showSelect = reactive({ value: false });
    let cache = reactive([]);
    const show_hide = function () {
      let statusquo = showSelect.value;
      // console.log(statusquo)
      if (!statusquo) {
        // 展开的逻辑
        showSelect.value = true;
      } else {
        // 取消的逻辑
        cancel();
      }
    };
    const cancel = function () {
      close();
    };
    const confirm = function () {
      console.log(selectList);
      update_cache(true);
      showSelect.value = false;
    };
    const close = function () {
      showSelect.value = false;
      update_cache(false);
    };
    const closespan = function (v) {
      cache = cache.filter((item) => item.id !== v.id);
      let seite = toRaw(store.state.productPage.payload[fieldKey].value);
      console.log(seite, selectList, cache, v);
      let deleteIndex = seite.findIndex((e) => {
        return e == v.id;
      });
      seite.splice(deleteIndex, 1);
      selectList.forEach((e) => {
        if (e.id == v.id) {
          e.selected = false;
        }
      });
      cache.forEach((e) => {
        if (e.id == v.id) {
          e.selected = false;
        }
      });
      console.log(seite, selectList, cache);
      store.commit("productPage/setPayload", {
        [fieldKey]: {
          value: seite,
        },
      });
    };
    let selectedItems = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        if (!value) {
          return [];
        }
        console.log(value.value);
        let a = value.value.map((e) => ({
          ...selectList.find((ee) => ee.id === e),
        }));
        console.log(
          "filedterd categorites_________________________________________________________________________________________________"
        );
        function unique(arr) {
          for (let i = 0; i < arr.length; i++) {
            for (var j = i + 1; j < arr.length; j++) {
              if (arr[i].id == arr[j].id) {
                //第一个等同于第二个，splice方法删除第二个
                arr.splice(j, 1);
                j--;
              }
            }
          }
          return arr;
        }
        a = unique(a);
        console.log(a);
        a.forEach((item) => (item.selected = true));
        return a;
      },
      set(val) {
        updateKey({ value: val });
      },
    });
    const state = reactive({
      selectedItems,
      showSelect,
      cache,
      show_hide,
      cancel,
      confirm,
      selectList,
      closespan,
    });
    // onMounted(async ()=>{
    //     try{
    //         const response1 = await $getEnvInfo();
    //         if (response1.Code === 200 && response1.msg === "success") {
    //             const {
    //                 // activitymodes,
    //                 // ages,
    //                 categories,
    //                 // cities,
    //                 // days,
    //                 features,
    //                 // theme_related,
    //             } = response1.details;
    //
    //             // 活动类别列表数据
    //             // console.log(categories,features)
    //             // console.log(fieldKey)
    //             // console.log(categories)
    //             if(fieldKey.indexOf('cat')!= -1){
    //                 categories.forEach(e=>{
    //                     selectList.push({
    //                         name:e.name,
    //                         id:e.id,
    //                         selected:false
    //                     })
    //                 })
    //
    //             }else if (fieldKey.indexOf('feat')!= -1){
    //                 features.forEach(e=>{
    //                     selectList.push({
    //                         name:e.name,
    //                         id:e.id,
    //                         selected:false
    //                     })
    //                 })
    //             }
    //
    //
    //             console.log(toRaw(selectList))
    //             // console.log(this.$data.fields)
    //
    //             // activity_days
    //         } else {
    //             throw(1)
    //         }
    //     }catch(e){
    //         console.log(e)
    //         console.log("基础配置信息获取失败，请尝试刷新页面")
    //     }
    //
    // })
    const addcache = function (e) {
      // 如果已经存在就需要移除
      if (e.selected) {
        cache = cache.filter((item) => item.id !== e.id);
      }
      let statusquo = e.selected;
      console.log(selectList);
      let targetIndex = selectList.findIndex((ee) => e.id == ee.id);
      console.log(targetIndex);
      selectList[targetIndex].selected = !statusquo;
      console.log(selectList);
      let item = { ...e };
      // console.log('itiem:',item)
      if (!statusquo) {
        cache.push(item);
        // console.log(cache)
      } else {
        let newse = cache.filter((ee) => ee.id != e.id);
        cache = newse;
      }
      console.log("added cache : ", cache);
    };
    const update_cache = function (forward) {
      if (forward) {
        console.log(cache);
        // cache状态 同步
        let newSelectedItems = cache.map((e) => {
          return {
            name: e.name,
            selected: e.selected,
            id: e.id,
          };
        });
        store.commit("productPage/setPayload", {
          [fieldKey]: {
            value: newSelectedItems.map((e) => e.id),
          },
        });
        selectList.forEach((e) => {
          let result = cache.find((ee) => e.id == ee.id);
          if (result) {
            let status = result.selected;
            e.selected = status;
          }
        });
      } else {
        // 实选列表 状态返回
        let seite = store.state.productPage.payload[fieldKey];
        if (seite) {
          seite = seite.value.map((e) => ({
            ...selectList.find((ee) => ee.id == e),
          }));
          cache = seite;
          selectList.forEach((e) => {
            let instatee = seite.find((ee) => ee.id == e.id);
            if (instatee) {
              e.selected = true;
            } else {
              e.selected = false;
            }
          });
        } else {
          cache = [];
          selectList.forEach((e) => {
            e.selected = false;
          });
          return;
        }
      }
    };
    state.addcache = addcache;
    return state;
  },
  methods: {},
};
</script>

