  name: 'InsSelectx',
  components: {
    FieldStructure,
    PopInfoBlack
  },
  props: {
    field: Object
  },
  setup(props, context) {
    // console.log(props)
    const isShow = ref(false)
    const isShow2 = ref(false)
    const isShow3 = ref(false)
    const showTxar2 = ref(false)
    const showTxar3 = ref(false)
    const arr = [
      { name: '天安·组织者责任险·每人赔偿限额10万档' },
      { name: '天安·组织者责任险·每人赔偿限额20万档' },
      { name: '天安·组织者责任险·每人赔偿限额30万档' }
    ]
    const getCurrentItem = ref('请选择保险购买方式')
    const getCurrentItem2 = ref('请选择购买的保险类型')
    const txpl =
      '例：您选择投保由平安财险承保的组织者保险，基础费率2元/天。每次事故没人赔偿限额10玩（其中医药费用赔偿限额为1完）；没人财产损失赔偿限额1000元；每次及累计赔偿限额100万，具体保障范围请详询保险公司或查看保单条款'
    const store = useStore()
    const { charLimit, fieldTitle, style, required, inputid, first, second } =
      toRaw(props.field)
    console.log(toRaw(props.field))
    const selected = reactive({
      one: false,
      two: false
    })
    watchEffect(() => {
      if (
        store.state.productPage.payload.liability_insurance_self_details?.value
      ) {
        isShow3.value = true
        getCurrentItem2.value =
          store.state.productPage.payload.liability_insurance_self_details.value
        getCurrentItem.value = '购买营探合作的第三方保险'
      }
    })
    // store.commit("productPage/setPayload", {
    //   [first]: {
    //     value: false,
    //   },
    // });
    // store.commit("productPage/setPayload", {
    //   [second]: {
    //     value: "",
    //   },
    // });
    const txarvalue = computed({
      get() {
        let value = store.state.productPage.payload[second]
        console.log('📕', value)
        if (!value) {
          // store.commit("productPage/setPayload", {
          //   [second]: {
          //     value: "",
          //   },
          // });
          return txpl
        }
        return value.value
      },
      set(val) {
        updateDetail(val)
      }
    })
    const showTxar = computed({
      get() {
        let value = store.state.productPage.payload[first]
        if (!value) {
          // store.commit("productPage/setPayload", {
          //   [first]: {
          //     value: false,
          //   },
          // });
          return false
        }
        return value.value
      },
      set(val) {
        updateDetail(val)
      }
    })
    const show = e => {
      // console.log(e)
      updateType(e)
    }
    const show2 = item => {
      getCurrentItem2.value = item.name
      store.commit('productPage/setPayload', {
        ['liability_insurance_self_details']: {
          value: item.name
        }
      })
      isShow2.value = false
      showTxar3.value = true
    }
    const ShowSelect = () => {
      isShow.value = true
    }
    const cancel = () => {
      showTxar2.value = false
    }
    const cancel2 = () => {
      showTxar3.value = false
    }
    const ShowSelect2 = () => {
      isShow2.value = true
    }
    const updateType = e => {
      console.log(Boolean(e - 1))
      if (e == 2) {
        getCurrentItem.value = '自行购买其他保险'
        isShow3.value = false
        showTxar2.value = true
        store.commit('productPage/setPayload', {
          ['liability_insurance_self_details']: {
            value: ''
          }
        })
      }
      if (e == 1) {
        getCurrentItem.value = '购买营探合作的第三方保险'
        isShow3.value = true
        console.log(getCurrentItem2.value)
        store.commit('productPage/setPayload', {
          ['liability_insurance_self_details']: undefined
        })
        if (getCurrentItem2.value !== '请选择购买的保险类型') {
          store.commit('productPage/setPayload', {
            ['liability_insurance_self_details']: {
              value: getCurrentItem2.value
            }
          })
        }
        console.log(
          store.state.productPage.payload['liability_insurance_self_details']
        )
      }
      store.commit('productPage/setPayload', {
        [first]: {
          value: Boolean(e - 1)
        }
      })
      isShow.value = false
    }
    const updateDetail = e => {
      store.commit('productPage/setPayload', {
        [second]: {
          value: e
        }
      })
    }
    const choosefile = e => {
      // console.log(e)
      // console.log(this)
      document.getElementById(inputid).click()
    }
    return {
      ShowSelect,
      ShowSelect2,
      isShow,
      isShow2,
      isShow3,
      cancel,
      cancel2,
      getCurrentItem,
      getCurrentItem2,
      charLimit,
      fieldTitle,
      style,
      required,
      selected,
      show,
      show2,
      inputid,
      choosefile,
      showTxar,
      showTxar2,
      showTxar3,
      txpl,
      txarvalue,
      first,
      second,
      arr
    }
  },
  methods: {
    init() {
      request({
        method: 'post',
        url: '/api/e9b849a515a84327b424af7ccdbf2949/v1_0_0/product/get',
        data: {
          id: useStore().state.productPage.id
        },
        auth: {
          username:
            'NginxAuthUsername16381efa069a90cbde99854c81183036a4bd2e4776eb770fa69740f1c5c06aa4',
          password:
            'NginxAuthPassworde0588e3b3c5b240fe3f5fb653d2990f34b52ee0b595acf6553a924b0716cb366'
        },
        headers: {
          'Content-Type': 'application/json'
        }
      }).then(res => {
        if ((res.Code = 200)) {
          // 如果是自己购买意外保险
          if (res?.details?.liability_insurance_self_if) {
            this.getCurrentItem = '自行购买其他保险'
          }
          // 购买营探合作保险
          if (res?.details?.liability_insurance_self_details) {
            this.getCurrentItem = '购买营探合作的第三方保险'
            this.getCurrentItem2 = res.details.liability_insurance_self_details
            this.isShow3 = true
          }
        } else {
          return
        }
      })
    }
  },
  mounted() {
    this.init()
  },
  data() {
    return {
      ifShowPop: false
    }
  }
}
</script>

