<template>
  <div class="food-sele">
    <div class="sel">
      <div class="sel-title">请选择餐饮提供方式</div>
      <!-- <select name="" id="" class="basic-input nomarginbot"
                    v-model="type">
                <option value="0">请选择</option>
                <option value="1">自制餐饮</option>
                <option value="2">外部采购</option>
                <option value="3">不提供餐饮</option>
            </select> -->
      <SelectInput
        :List="selectList"
        :value="type"
        @sendValue="changeType"
        title="请选择餐饮提供方式"
      ></SelectInput>
      <div class="des">
        <textarea
          v-model="description"
          placeholder="请对活动餐饮情况进行描述"
          class="basic-input dest"
          name=""
          id=""
          cols="30"
          rows="10"
        ></textarea>
      </div>
    </div>
    <!-- <div class="pic">
            <div class="up">
                <div class="inp" @click="choose">

                    <div class="uppic" ></div>
                    上传相关证明
                            <input type="file" name="" id="baaa" v-show="false">
                </div>
                <div class="so">支持扩展名： .rar .zip .doc .docx .pdf .jpg...</div>
            </div>
        </div> -->
  </div>
</template>

<script>
import { toRaw } from "@vue/reactivity";
import { useStore } from "vuex";
import { computed } from "@vue/runtime-core";
import SelectInput from "./SelectInput.vue";
export default {
  props: {
    field: Object,
  },
  components: {
    SelectInput,
  },
  setup(props, context) {
    // console.log(props)
    const store = useStore();

    const { fieldTitle, fieldKey } = toRaw(props.field);

    // console.log('style: ',style)
    store.commit("productPage/setPayload", {
      [fieldKey]: {
        value: {
          type: 0,
          description: "",
        },
      },
    });
    const globalValue = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        console.log(value);
        console.log(fieldKey);
        if (!value) {
          store.commit("productPage/setPayload", {
            [fieldKey]: {
              value: {
                type: 1,
                description: "223",
              },
            },
          });
          value = store.state.productPage.payload[fieldKey];
        }
        return value.value;
      },
      set(val) {
        console.log(val);
      },
    });
    const type = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];

        if (!value) {
          store.commit("productPage/setPayload", {
            [fieldKey]: {
              value: {
                type: 0,
                description: "",
              },
            },
          });
          value = store.state.productPage.payload[fieldKey];
        }
        return value.value.type;
      },
      set(val) {
        let value = toRaw(store.state.productPage.payload[fieldKey]);
        value = value.value;
        value.type = val;
        store.commit("productPage/setPayload", {
          [fieldKey]: {
            value,
          },
        });
      },
    });
    const description = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];

        if (!value) {
          store.commit("productPage/setPayload", {
            [fieldKey]: {
              value: {
                type: 0,
                description: "",
              },
            },
          });
          value = store.state.productPage.payload[fieldKey];
        }
        return value.value.description;
      },
      set(val) {
        let value = toRaw(store.state.productPage.payload[fieldKey]);
        value = value.value;
        value.description = val;
        store.commit("productPage/setPayload", {
          [fieldKey]: {
            value,
          },
        });
      },
    });
    const changeType = (e) => {
      type._setter(e);
    };

    return {
      globalValue,
      fieldTitle,
      fieldKey,
      type,
      description,
      changeType,
    };
  },
  methods: {
    choose() {
      document.getElementById("baaa").click();
    },
  },
  data() {
    return {
      selectList: [
        { value: 1, option: "自制餐饮" },
        { value: 2, option: "外部餐饮" },
        { value: 3, option: "不提供餐饮" },
      ],
    };
  },
};
</script>

