<template>
  <field-structure :fieldTitle="fieldTitle">
    <div class="single-select">
      <!-- <select 
            name="" id="" class="basic-input" 
            v-model="unified">
                <option :value="1">不统一服装</option>
                <option :value="2">统一服装</option>
                
               
            </select> -->
      <SelectInput
        :List="selectList"
        :value="unified"
        @sendValue="changeType"
        title="请选择是否统一服装"
      ></SelectInput>
      <div class="detail" v-show="Boolean(unified - 1)">
        <textarea
          v-model="detail"
          placeholder="请输入服装要求"
          class="basic-input ee"
          name=""
          id=""
          cols="30"
          rows="10"
        ></textarea>
      </div>
    </div>
  </field-structure>
</template>

<script>
import { computed, ref, toRaw } from "@vue/runtime-core";
import { useStore } from "vuex";
import FieldStructure from "./FieldStructure.vue";
import SelectInput from "./SelectInput.vue";
export default {
  name: "SimpleText",
  components: {
    FieldStructure,
    SelectInput,
  },
  props: {
    field: Object,
  },
  setup(props, context) {
    const store = useStore();
    const valueLength = ref(0);
    let { charLimit, fieldTitle, fieldKey, ins } = toRaw(props.field);
    if (!charLimit) {
      charLimit = 20;
    }
    store.commit("productPage/setPayload", {
      [fieldKey]: {
        value: {
          unified: false,
          detail: "",
        },
      },
    });
    const unified = computed({
      get() {
        let value = toRaw(store.state.productPage.payload[fieldKey]);
        if (!value) {
          store.commit("productPage/setPayload", {
            [fieldKey]: {
              value: {
                unified: false,
                detail: "",
              },
            },
          });
        }
        value = toRaw(store.state.productPage.payload[fieldKey]);
        value = value.value;
        if (value.unified) {
          return 2;
        }

        return 1;
      },
      set(val) {
        console.log(val);
        let value = toRaw(store.state.productPage.payload[fieldKey]);
        value = value.value;
        if (val - 1) {
          value.unified = true;
        } else {
          value.unified = false;
          value.detail = "";
        }
        store.commit("productPage/setPayload", {
          [fieldKey]: {
            value,
          },
        });
      },
    });

    const detail = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        if (!value) return "";
        return value.value.detail;
      },
      set(e) {
        store.commit("productPage/setPayload", {
          [fieldKey]: {
            value: {
              unified: true,
              detail: e,
            },
          },
        });
      },
    });
    const changeType = (e) => {
      unified._setter(e);
    };

    return {
      unified,
      detail,
      fieldTitle,
      fieldKey,
      valueLength,
      charLimit,
      ins,
      changeType,
    };
  },
  data() {
    return {
      selectList: [
        { value: 1, option: "不统一服装" },
        { value: 2, option: "统一服装" },
      ],
    };
  },
};
</script>

<style lang="less" scoped>
.ee {
  margin-top: 20px;
  height: 100px;
}
select {
  margin-bottom: 10px;
}
</style>
