<template>
  <field-structure :fieldTitle="field.fieldTitle" :required="false">
    <div class="click-to">
      <div class="outer-items">
        <div class="aaa">
          <div class="add-item dir-user-defined">
            <div
              class="se-item greyed"
              v-for="(v, ind) in featureA"
              :key="ind"
              :class="'se-it'"
            >
              {{ v }}
              <span class="closespan" @click="closespan(v)"> X </span>
            </div>
            <div class="bySelf" @click="addItem">
              自定义 <img src="./resource/down.png" alt="" />
            </div>
          </div>
        </div>
      </div>

      <div class="to-select" v-show="showSelect">
        <div class="custom" v-show="showCustom">
          <div class="se-title">
            自定义活动特色<span class="close" @click="this.showCustom = false"
              >X</span
            >
          </div>
          <div class="inp" style="height: 280px">
            <input
              type="text"
              name="newfea"
              id=""
              v-model="newFeature"
              placeholder="输入要添加的新特色"
            />
          </div>
          <div class="bot">
            <div class="rightf">
              <div class="cancel button" @click="this.showCustom = false">
                取消
              </div>
              <div class="submit button" @click="addFeature">确定</div>
            </div>
          </div>
        </div>
        <div class="se-title">
          {{ field.fieldTitle }} (可多选)<span
            class="close"
            @click="this.showSelect = false"
            >X</span
          >
        </div>
        <div class="middle">
          <div
            class="se-item"
            v-for="(v, ind) in selectList"
            :key="ind"
            :class="v.selected ? 'se-it' : ''"
            @click="addToSelect(v)"
          >
            {{ v.name }}
          </div>
        </div>
        <div class="bot">
          <div class="rightf">
            <div class="in">已选{{ toSelect.length }}项</div>
            <div class="cancel button" @click="this.showCustom = true">
              添加自定义特色
            </div>
            <div class="cancel button" @click="cancel">取消</div>
            <div class="submit button" @click="confirm">确定</div>
          </div>
        </div>
      </div>
    </div>
  </field-structure>
</template>

<script>
import { toRaw } from "@vue/reactivity";
import { ElMessage } from "element-plus";
import FieldStructure from "./FieldStructure.vue";
export default {
  name: "FeatureSelect",
  components: {
    FieldStructure,
  },
  props: {
    field: Object,
  },
  created() {
    let proplist = this.$props.field.selectList;
    this.$data.selectList = proplist.map((e, indd) => ({
      name: e,
      selected: false,
      indd,
    }));
    // this.$bus
  },
  data() {
    return {
      showCustom: false,
      showSelect: false,
      selectList: [],
      selectedItems: [],
      toSelect: [],
      newFeature: "",
    };
  },
  computed: {
    getButton() {
      return ".v-user-defined";
    },
    featureA() {
      let fieldKey = this.$props.field.fieldKey;
      let va = this.$store.state.productPage.payload[fieldKey];
      if (va) {
        va = va.value;
      }
      if (va?.length >= 7 || va?.length < 2) {
        ElMessage({
          type: "info",
          message: "请选择2-7个活动特色",
        });
      }
      return va?.slice(0, 7);
    },
  },
  methods: {
    close() {
      this.showSelect = false;
    },
    addItem() {
      this.showSelect = !this.showSelect;
      if (this.showSelect === true) {
        this.$bus.emit("pop");
      }
    },
    closespan(v) {
      // console.log(v)
      let seite = toRaw(
        this.$store.state.productPage.payload[this.$props.field.fieldKey].value
      );
      //    console.log(seite)
      let deleteIndex = seite.findIndex((e) => e == v);
      seite.splice(deleteIndex, 1);

      this.$data.selectList.forEach((e) => {
        if (e.name == v) {
          e.selected = false;
        }
      });
      this.$props.field.fieldKey;

      let newTOselet = [];
      this.selectList.forEach((e) => {
        if (e.selected) {
          newTOselet.push({ ...e });
        }
      });

      this.toSelect = newTOselet;

      this.$store.commit("productPage/setPayload", {
        [this.$props.field.fieldKey]: {
          value: seite,
        },
      });
    },
    addToSelect(e) {
      // console.log(e)
      let statusquo = e.selected;
      this.selectList[e.indd].selected = !statusquo;
      if (!statusquo) {
        this.toSelect.push(e);
      } else {
        let newse = this.toSelect.filter((ee) => ee.indd != e.indd);
        this.toSelect = newse;
      }
    },
    cancel() {
      this.toSelect = [];
      this.close();
    },
    confirm() {
      this.selectedItems = this.toSelect;
      let strArr = this.selectedItems.map((e) => e.name);
      let fieldKey = this.$props.field.fieldKey;
      this.$store.commit("productPage/setPayload", {
        [fieldKey]: {
          value: strArr,
        },
      });

      this.close();
    },
    addFeature() {
      let newFeatureObj = {
        name: this.newFeature,
        selected: false,
        indd: this.$data.selectList.length,
      };
      this.$data.selectList.push(newFeatureObj);
      this.addToSelect(newFeatureObj);
      this.showCustom = false;
    },
  },
};
</script>

