<template>
  <ModuleTitle :moduleTitle="title">
    <PeopleLimit />

    <AgeLimit />

    <ActivityForm />

    <Difficulty />

    <ActivityType />

    <TeacherRatio />

    <Language />
  </ModuleTitle>
</template>

<script>
import ModuleTitle from "../ModuleTitle.vue";
import CommonModule from "../CommonModule.vue";
import SimpleText from "../../inputs/SimpleText.vue";
import PeopleLimit from "@/pages/internal/views/providerTools/activityCreateForm/Modules/2_CourseInfo/components/PeopleLimit.vue";
import AgeLimit from "@/pages/internal/views/providerTools/activityCreateForm/Modules/2_CourseInfo/components/AgeLimit.vue";
import ActivityForm from "@/pages/internal/views/providerTools/activityCreateForm/Modules/2_CourseInfo/components/ActivityForm.vue";
import ActivityType from "@/pages/internal/views/providerTools/activityCreateForm/Modules/2_CourseInfo/components/ActivityType.vue";
import TeacherRatio from "@/pages/internal/views/providerTools/activityCreateForm/Modules/2_CourseInfo/components/TeacherRatio.vue";
import Difficulty from "@/pages/internal/views/providerTools/activityCreateForm/Modules/2_CourseInfo/components/Difficulty.vue";
import Language from "@/pages/internal/views/providerTools/activityCreateForm/Modules/2_CourseInfo/components/Language.vue";
import { toRaw } from "@vue/reactivity";

export default {
  name: "CourseInfo",
  components: {
    CommonModule,
    ModuleTitle,
    SimpleText,
    PeopleLimit,
    AgeLimit,
    ActivityForm,
    ActivityType,
    TeacherRatio,
    Difficulty,
    Language,
  },
  created() {
    this.$bus.$on("showKeys", () => {
      let title = toRaw(this.title);
      let keys = [];
      toRaw(this.fields).forEach((element) => {
        // console.log(element)
        let obj;
        if (element.fieldKey instanceof Array) {
          element.fieldKey.forEach((e) =>
            keys.push({
              title: element.fieldTitle,
              key: e,
            })
          );
        } else {
          keys.push({
            title: element.fieldTitle,
            key: element.fieldKey,
          });
        }
      });
      console.log("title:", title);
      console.log("keys:", keys);
      let finished = false;

      for (let i = 0; i < keys.length; i++) {
        let value = this.$store.state.productPage.payload[keys[i].key];
        // console.log(value)
        if (value == undefined) {
          console.log("unde fined");
          console.log("payload key; ", keys[i].key);
          console.log("key title; ", keys[i].title);
          this.setModuleEmpty(keys[i].title);
          console.log(
            this.title,
            " returned after return_________________________________________________"
          );
          return;
        }
      }

      this.setModuleFinished();
      console.log(
        this.title,
        " set done_________________________________________________"
      );
    });
  },
  data() {
    return {
      title: "课程详情",
      fields: [
        ["活动人数", "size_valid"],
        ["报名年龄", ["age_min", "age_max"]],
        ["日程形式", ["stay_if", "activity_mode"]],
        ["难易度", "difficulty"],
        ["活动类型", "activity_type"],
        ["师生比", "teacher_student"],
        ["支持语言", "language"],
      ].map((e) => {
        let o = {};
        o.fieldTitle = e[0];
        o.fieldKey = e[1];
        for (let i in e[2]) {
          o[i] = e[2][i];
        }
        // console.log(o)
        return o;
      }),
    };
  },
  methods: {
    setModuleFinished() {
      this.$store.commit("productPage/updateModule", {
        moduleTitle: this.title,
        name: "",
        done: true,
        selected: false,
        empty: "",
      });
    },
    setModuleEmpty(title) {
      console.log("set empty ");
      console.log("this.titls:", this.title);
      console.log("empty.titls:", title);
      this.$store.commit("productPage/updateModule", {
        moduleTitle: this.title,
        name: title,
        done: false,
        selected: false,
        empty: title,
      });
    },
  },
};
</script>

