<template>
  <field-structure :fieldTitle="fieldTitle">
    <div class="beforeCamp">
      <div class="times">
        <div class="time-type">
          <DatePicker2 :value="date" @valueChanged="dateChanged"></DatePicker2>
          &nbsp;&nbsp;&nbsp;
          <div class="input">
            <DayTimeSelect
              @sendValue="changeTimeValue($event, 'start_time')"
              :value="start_time"
              width="110"
            ></DayTimeSelect>
            <!-- <input type="time" v-model="start_time">  -->
          </div>
          &nbsp;→&nbsp;
          <div class="input">
            <DayTimeSelect
              @sendValue="changeTimeValue($event, 'end_time')"
              :value="end_time"
              width="110"
            ></DayTimeSelect>
          </div>
          <!--                    <span class="">开始时间： </span>-->
          <!--                    <input type="date" name="" id=""-->

          <!--                    v-model="start_time"-->
          <!--                    placeholder=""-->
          <!--                    >-->
          &nbsp;&nbsp;
          <input
            v-model="type"
            type="text"
            class="basic-input"
            placeholder="请输入参会方式"
          />
        </div>
        <!--            <div class="time-type">-->
        <!--                     <span class="">结束时间： </span>-->
        <!--                    <input type="date" name="" id=""-->

        <!--                    v-model="end_time"-->
        <!--                    placeholder=""-->
        <!--                    >-->

        <!--            </div>-->
      </div>
      <div class="word-box">
        请填写说明会内容 <br />
        <textarea
          v-model="detail"
          name=""
          id=""
          cols="50"
          rows="10"
          class="basic-input detail"
          placeholder="如：1.在线破冰活动
       2.在线安全培训
       3.答疑，请出行人及监护人积极参加"
        ></textarea>
      </div>
    </div>
  </field-structure>
</template>

<script>
import FieldStructure from "./FieldStructure.vue";
import DateSpanInput from "./DateSpanInput.vue";
import SingleTextArea from "./SingleTextArea.vue";
import DayTimeSelect from "./DayTimeSelect.vue";
import { computed, toRaw, useSlots } from "@vue/runtime-core";
import { useStore } from "vuex";
import DatePicker2 from "@/pages/internal/views/providerTools/activityCreateForm/inputs/DatePicker2";
import { ref } from "vue";

export default {
  components: {
    FieldStructure,
    DateSpanInput,
    SingleTextArea,
    DatePicker2,
    DayTimeSelect,
  },
  props: {
    field: Object,
  },
  setup(props, conotext) {
    let store = useStore();
    let { fieldKey, fieldTitle } = toRaw(props.field);

    let date = ref("");
    let entity = toRaw(store.state.productPage.payload[fieldKey]);
    if (entity !== undefined && entity.value.end_time !== undefined)
      date.value = new Date(entity.value.end_time).toISOString().substr(0, 10);

    store.commit("productPage/setPayload", {
      [fieldKey]: {
        value: {
          start_time: new Date().toISOString(),
          end_time: new Date().toISOString(),
          type: "",
          detail: "",
        },
      },
    });
    const start_time = computed({
      get() {
        let entity = toRaw(store.state.productPage.payload[fieldKey]);
        if (entity === undefined || entity.value.start_time === undefined)
          return "";
        return entity.value.start_time;
      },
      set(e) {
        let entity = toRaw(store.state.productPage.payload[fieldKey]);
        if (entity) {
          entity = entity.value;
        } else {
          entity = {};
        }
        let date;
        if (entity.start_time) {
          date = new Date(entity.start_time);
        } else {
          date = new Date();
        }
        date.setUTCMinutes(Number(e.substr(3, 2)));
        date.setUTCHours(Number(e.substr(0, 2)));
        entity.start_time = date.toISOString();
        store.commit("productPage/setPayload", {
          [fieldKey]: {
            value: entity,
          },
        });
      },
    });
    const end_time = computed({
      get() {
        let entity = toRaw(store.state.productPage.payload[fieldKey]);
        if (entity === undefined || entity.value.end_time === undefined)
          return "";
        return entity.value.end_time;
      },
      set(e) {
        let entity = toRaw(store.state.productPage.payload[fieldKey]);
        if (entity) {
          entity = entity.value;
        } else {
          entity = {};
        }
        let date;
        if (entity.end_time) {
          date = new Date(entity.end_time);
        } else {
          date = new Date();
        }
        date.setUTCMinutes(Number(e.substr(3, 2)));
        date.setUTCHours(Number(e.substr(0, 2)));
        entity.end_time = date.toISOString();
        store.commit("productPage/setPayload", {
          [fieldKey]: {
            value: entity,
          },
        });
      },
    });
    const type = computed({
      get() {
        let value = toRaw(store.state.productPage.payload[fieldKey]);
        if (!value) return "";
        return value.value.type;
      },
      set(e) {
        let value = toRaw(store.state.productPage.payload[fieldKey]);
        if (value) {
          value = value.value;
        } else {
          value = {};
        }
        value.type = e;

        store.commit("productPage/setPayload", {
          [fieldKey]: {
            value,
          },
        });
      },
    });
    const detail = computed({
      get() {
        let value = toRaw(store.state.productPage.payload[fieldKey]);
        if (!value) return "";
        return value.value.detail;
      },
      set(e) {
        let value = toRaw(store.state.productPage.payload[fieldKey]);
        if (value) {
          value = value.value;
        } else {
          value = {};
        }
        value.detail = e;

        store.commit("productPage/setPayload", {
          [fieldKey]: {
            value,
          },
        });
      },
    });

    const checkchange = (e) => {
      console.log(e.target.value);
      console.log(typeof e.target.value);
      // console.log(e.target.value)
      // console.log(typeof e.target.value)
      // e.target.value = '2022-07-03'
    };
    function date2string(date) {
      let yaer = date.getFullYear();
      let month = date.getMonth() + 1;
      if (month < 10) {
        month = "0" + month;
      }
      let datea = date.getDate();
      if (datea < 10) {
        datea = "0" + datea;
      }
      let inn = "-";
      return yaer + inn + month + inn + datea;
    }

    return {
      checkchange,
      fieldKey,
      fieldTitle,
      start_time,
      end_time,
      type,
      detail,
      date,
    };
  },
  data() {
    return {};
  },
  methods: {
    dateChanged(val) {
      this.date = val;
      console.log(this.date);
    },
    changeTimeValue(val, type) {
      let fieldKey = this.$props.field.fieldKey;
      let entity = this.$store.state.productPage.payload[fieldKey];
      if (entity) {
        entity = entity.value;
      } else {
        entity = {};
      }
      entity[type] = val;
      this.$store.commit("productPage/setPayload", {
        [fieldKey]: {
          value: entity,
        },
      });
    },
  },
  watch: {
    date(val) {
      let entity = toRaw(this.$store.state.productPage.payload["briefing"]);
      if (entity) {
        entity = entity.value;
      } else {
        entity = {};
      }
      let date;
      if (entity.start_time) {
        date = new Date(entity.start_time);
      } else {
        date = new Date();
      }
      date.setUTCFullYear(Number(val.substr(0, 4)));
      date.setUTCMonth(Number(val.substr(5, 2)) - 1);
      date.setUTCDate(Number(val.substr(8, 2)));
      entity.start_time = date.toISOString();

      if (entity.end_time) {
        date = new Date(entity.end_time);
      } else {
        date = new Date();
      }
      date.setUTCFullYear(Number(val.substr(0, 4)));
      date.setUTCMonth(Number(val.substr(5, 2)) - 1);
      date.setUTCDate(Number(val.substr(8, 2)));
      entity.end_time = date.toISOString();

      console.log(entity);
      this.$store.commit("productPage/setPayload", {
        ["briefing"]: {
          value: entity,
        },
      });
    },
  },
};
</script>

