<template>
  <div class="label">价格说明:</div>
  <div class="content">
    <span>本商品为含税价，包含</span>
    <textarea
      rows="6"
      placeholder="请输入......"
      v-model="include_item"
    ></textarea>
    <div
      style="top: 35px"
      class="img"
      :class="ifactived1 ? 'actived' : 'unactived'"
      @click="ifactived1 = !ifactived1"
    >
      <pop-info
        :font_size="0.9"
        :top="-25"
        :left="30"
        @click.stop=""
        v-show="ifactived1"
      >
        <h3>输入提示</h3>
        出⾏⼈在活动期间的⻝宿费⽤、X项⽬体验费、课程师资费、场地费、教具物资费；餐费；
        ⼈⾝意外险保险费等</pop-info
      >
    </div>

    <span>本价格未包含的费用，包括但不限于</span>
    <textarea
      rows="8"
      placeholder="请输入......"
      v-model="exclude_item"
    ></textarea>
    <div
      style="top: 250px"
      class="img"
      :class="ifactived2 ? 'actived' : 'unactived'"
      @click="ifactived2 = !ifactived2"
    >
      <pop-info
        :font_size="0.9"
        :top="-25"
        :left="30"
        @click.stop=""
        v-show="ifactived2"
      >
        <h3>输入提示</h3>
        未包含费用中应当包括：1.收费提供的服务项目，要标注服务内容及对应收费价格范围；
        2.其它个人消费是必须包含的内容；
        3.活动过程中其它可选择性的活动自费项目价格；如：洗漱用具，各地和集散地之间往返的交通费、X门票费用、SPA馆体验费、干洗费、住宿升级以及其他个人消费；
        4.开具专用发票需由消费者自行补缴的税费</pop-info
      >
    </div>

    <span>产品包含的保险情况说明</span>
    <textarea
      rows="8"
      placeholder="请输入......"
      v-model="insurance_info"
    ></textarea>
    <div
      style="top: 510px"
      class="img"
      :class="ifactived3 ? 'actived' : 'unactived'"
      @click="ifactived3 = !ifactived3"
    >
      <pop-info :font_size="0.9" :top="-25" :left="30" v-show="ifactived3">
        <h3>输入提示</h3>
        如：已为您投保由天安财险承保的⼈⾝意外保险，保额10万元/⼈
        （组织者责任险情况不⽤在此说明，仅显⽰⼈⾝意外险的投保情况）保障范围为：
        个⼈意外10万元，其中5万元⾼⻛险运动保障，另外救援服务2万元，个⼈财产遗失1万元，
        公共交通（不含⾃驾）2万元，意外伤害10万元，
        具体保障范围可根据保险名称查询保险保单条款</pop-info
      >
    </div>

    <span>特殊条件退订机制</span>
    <div class="last" disabled rows="6">
      超出无损退订期后，因特定情形导致无法按时履约的，用户可在商品和服务活动开始日期前联系营探客服，提交“特殊原因退订”申请；符合特殊原因退订要求的，营探将按照下述约定向用户提供退款服务，不再适用“有条件退”的具体方案，详情请见《预订退费须知》
    </div>
    <!-- <label for="agreed"><input id="agreed" type="radio" value="true" v-model="agreed" @click.stop="changeRadio"> 已阅读并同意特殊条件退订机制</label> -->
  </div>
  <div class="clear"></div>
</template>

<script>
import { useStore } from "vuex";
import { computed } from "@vue/runtime-core";
import popInfo from "@/pages/internal/views/providerTools/activityCreateForm/inputs/popInfo";

export default {
  name: "PriceDesc",
  setup(props, context) {
    const store = useStore();
    const include_item = computed({
      get() {
        let entity = store.state.productPage.payload["price_include"];
        if (entity) return entity.value;
        return "";
      },
      set(val) {
        let payload = {};
        payload["price_include"] = { value: val };
        store.commit("productPage/setPayload", payload);
      },
    });

    const exclude_item = computed({
      get() {
        let entity = store.state.productPage.payload["price_exclude"];
        if (entity) return entity.value;
        return "";
      },
      set(val) {
        let payload = {};
        payload["price_exclude"] = { value: val };
        store.commit("productPage/setPayload", payload);
      },
    });

    const insurance_info = computed({
      get() {
        let entity = store.state.productPage.payload["price_insurance_detail"];
        if (entity) return entity.value;
        return "";
      },
      set(val) {
        let payload = {};
        payload["price_insurance_detail"] = { value: val };
        store.commit("productPage/setPayload", payload);
      },
    });
    return {
      include_item,
      exclude_item,
      insurance_info,
    };
  },
  data() {
    return {
      ifactived1: false,
      ifactived2: false,
      ifactived3: false,
      agreed: true,
    };
  },
  methods: {
    changeRadio() {
      this.agreed = !this.agreed;
    },
  },
  components: {
    popInfo,
  },
};
</script>

