<template>
  <field-structure :fieldTitle="field.fieldTitle">
    <div class="assem-flex" ref="box">
      <div class="assem-point">
        <div class="display-loc" @click="switchTable">
          <span>{{ pointTri[0] }}/{{ pointTri[1] }}/{{ pointTri[2] }}</span>
          <span v-show="showTable.value" class="right-ar"> ↑</span>
          <span v-show="!showTable.value" class="right-ar">↓</span>
        </div>
        <div v-show="showTable.value" class="select-table">
          <div class="fir-clas clas">
            <div
              class="fi singledis"
              v-for="(v, ind) in listOne"
              :key="ind"
              :class="v.selectedDis ? 'selectedDis' : ''"
              @click="setFirst(v)"
            >
              <span>{{ v.name }}</span>
              <span>></span>
            </div>
          </div>
          <div class="sec-clas clas">
            <div
              class="se singledis"
              v-for="(v, ind) in listTwo"
              :key="ind"
              :class="v.selectedDis ? 'selectedDis' : ''"
              @click="setSec(v)"
            >
              <span>{{ v.name }}</span>
              <span>></span>
            </div>
          </div>
          <div class="thi-clas clas">
            <div
              class="th singledis"
              v-for="(v, ind) in listThree"
              :key="ind"
              :class="v.selectedDis ? 'selectedDis' : ''"
              @click="setThree(v)"
            >
              {{ v.name }}
            </div>
          </div>
        </div>
      </div>
      <slot></slot>
    </div>
  </field-structure>
</template>

<script>
import FieldStructure from "./FieldStructure.vue";
import List from "../../../../../internal/assets/json/city.json";
import { computed, reactive, toRaw } from "@vue/runtime-core";
import { storeKey, useStore } from "vuex";
import store from "@/pages/internal/store/store";
import { ref } from "@vue/reactivity";

import { onMounted } from "@vue/runtime-core";

export default {
  components: {
    FieldStructure,
  },
  props: {
    field: Object,
  },
  setup(props, context) {
    const store = useStore();
    let { fieldTitle, fieldKey } = toRaw(props.field);
    let showTable = reactive({ value: false });
    const listOne = Object.keys(List).map((e) => ({
      name: e,
      selectedDis: false,
    }));
    let listTwo = reactive([]);

    let listThree = reactive([]);
    const pointTri = computed({
      get() {
        // 从props中获取的fieldKey是location_，需要处理后再使用
        let locationSuffixList = ["province", "city", "district"];
        // 获取完整的地区名数组
        let locationAllName = locationSuffixList.map((suffixName) => {
          // 拼接完整的键名并从Vuex中获取对应数据
          const allFieldKey = `${fieldKey}_${suffixName}`;
          let itemValue = store.state.productPage.payload[allFieldKey];
          return itemValue?.value;
        });
        // 由于可能出现[undefined,undefined,undefined]而返回true的情况，因此要先确认信息是否可以被渲染
        let couldShow = locationAllName.every((item) => {
          return item;
        });
        if (!couldShow) {
          return ["省", "市", "区/县"];
        }
        store.commit("productPage/setPayload", {
          [fieldKey]: { value: locationAllName },
        });
        return locationAllName;
      },
      set(val) {
        console.log(val);
      },
    });

    const updatePoint = function (index, name) {
      let obj = store.state.productPage.payload[fieldKey];
      if (!obj) {
        obj = {
          value: ["省", "市", "区/县"],
        };
      }
      let value = toRaw(obj.value);
      const location = new Map([
        [0, "province"],
        [1, "city"],
        [2, "district"],
      ]);
      value[index] = name;
      store.commit("productPage/setPayload", {
        [`${fieldKey}_${location.get(index)}`]: {
          value: name,
        },
      });
      console.log(store);
      pointTri.value.splice(index, 1, name);
      store.commit("productPage/setPayload", {
        [fieldKey]: { value: pointTri.value },
      });
    };
    const switchTable = function () {
      let statusquo = showTable.value;
      showTable.value = !statusquo;
    };
    const setFirst = function (v) {
      listOne.forEach((e) => {
        e.selectedDis = false;
      });

      v.selectedDis = true;
      let addtwo = Object.keys(List[v.name]).map((e) => ({
        name: e,
        selectedDis: false,
      }));
      let length = listTwo.length;
      for (let ii = 0; ii < length; ii++) {
        listTwo.pop();
      }
      addtwo.forEach((e) => {
        listTwo.push(e);
      });

      updatePoint(0, v.name);
      updatePoint(1, "市");
      updatePoint(2, "区/县");
      let length1 = listThree.length;
      for (let ii = 0; ii < length1; ii++) {
        listThree.pop();
      }
    };
    const setSec = (v) => {
      listTwo.forEach((e) => {
        e.selectedDis = false;
      });
      v.selectedDis = true;
      // console.log(this.bu,v)
      // console.log(List[this.bu])
      // console.log(List[this.bu][v])
      // console.log(v.name)
      let addthree = List[pointTri.value[0]][v.name].map((e) => ({
        name: e,
        selectedDis: false,
      }));
      //    console.log(addthree)
      //    console.log(listThree)
      let length = listThree.length;
      for (let ii = 0; ii < length; ii++) {
        listThree.pop();
      }
      addthree.forEach((e) => {
        listThree.push(e);
      });
      // console.log(listThree)
      // console.log(this.listThree)
      updatePoint(1, v.name);
      updatePoint(2, "区/县");
    };
    const setThree = (v) => {
      listThree.forEach((e) => {
        e.selectedDis = false;
      });
      v.selectedDis = true;
      // this.listThree = Object.keys(List[this.bu][this.ting][v])
      updatePoint(2, v.name);
    };

    let box = ref(null);
    let showPickers = reactive({ value: false });
    //挂载
    const switchPicker = function () {
      console.log("switch   a");
      let statue = showPickers.value;
      console.log(statue);
      showPickers.value = !statue;
    };
    const closePicker = function () {
      showTable.value = false;
    };
    onMounted(() => {
      window.addEventListener("mousedown", (e) => {
        // 获取被点击的元素
        const clickedEl = e.target;
        // `el` 是你正在检测外部点击的元素
        if (!box.value?.contains) {
          closePicker();
          return;
        }
        if (box.value.contains(clickedEl)) {
          // 单击“el”内部
        } else {
          // 在`el`之外点击
          closePicker();
        }
      });
    });

    return {
      fieldTitle,
      fieldKey,
      showTable,
      switchTable,
      pointTri,
      setFirst,
      setSec,
      setThree,
      listOne,
      listTwo,
      listThree,
      box,
    };
  },
  data() {
    return {};
  },
  methods: {},
  created() {},
};
</script>

<style lang="less" scoped>
.assem-point {
  position: relative;
  margin-right: 50px;
}
.display-loc {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;

  width: 280px;
  height: 32px;
  border: 1px solid #ddd;
  border-radius: 5px;

  color: #888;
}
.select-table {
  position: absolute;
  top: 50px;

  display: flex;

  width: 400px;
  // height: 260px;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 2px 2px 5px #ddd, -2px -2px 5px #ddd;
  z-index: 1;
}
.clas {
  width: 134px;
  height: inherit;
  padding: 0 5px;
  border-right: 1px solid #ddd;
}
.right-ar {
  position: absolute;
  right: 12px;
}
.selectedDis {
  background-color: #f2ffd7;
  color: #a3dc26;
}
.singledis {
  display: flex;
  justify-content: space-between;
}
.assem-flex {
  display: flex;
}
</style>
