<template>
  <field-structure :fieldTitle="fieldTitle">
    <div class="single-checkbox" @click="checksw">
      <div class="box" @click="check" :class="{ active: globalValue }">
        <div class="gou" v-show="globalValue">√</div>
      </div>
      <div class="name">{{ check_item }}</div>
    </div>
  </field-structure>
</template>

<script>
import { computed, reactive, ref, toRaw } from "@vue/runtime-core";
import { useStore } from "vuex";
import FieldStructure from "./FieldStructure.vue";
export default {
  name: "SimpleText",
  components: {
    FieldStructure,
  },
  props: {
    field: Object,
  },
  setup(props, context) {
    const store = useStore();
    const valueLength = ref(0);
    let { charLimit, fieldTitle, fieldKey, ins, check_item } = toRaw(
      props.field
    );
    if (!charLimit) {
      charLimit = 20;
    }
    const check_value = reactive({ selected: false });
    const globalValue = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        if (!value) {
          return false;
        }
        return value.value;
      },
      set(val) {
        updateKey(val);
      },
    });
    const updateKey = (val) => {
      store.commit("productPage/setPayload", {
        [fieldKey]: {
          value: val,
        },
      });
    };

    return {
      globalValue,
      fieldTitle,
      fieldKey,
      valueLength,
      charLimit,
      ins,
      check_item,
      check_value,
    };
  },
  methods: {
    checksw() {
      // console.log('aa')
      this.globalValue = !this.globalValue;
    },
  },
};
</script>

<style lang="less" scoped>
.box {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 16px;
  height: 16px;
  border: 1px solid #d9d9d9;
  margin-right: 10px;
}
.active {
  background: #92d401;
  color: white;
}
.single-checkbox {
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
