<template>
  <div class="module">
    <div :id="moduleTitle"></div>
    <div class="module-title">{{ moduleTitle }}</div>
    <div class="module-fields">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import { toRaw } from "@vue/reactivity";
export default {
  created() {
    console.log("mudule created: ");
    console.log(this.title, "created");
    console.log(this.fields);
    this.$bus.$on("showKeys", () => {
      let title = toRaw(this.title);
      let keys = [];
      toRaw(this.fields).forEach((element) => {
        // console.log(element)
        let obj;
        if (element.fieldKey instanceof Array) {
          element.fieldKey.forEach((e) =>
            keys.push({
              title: element.fieldTitle,
              key: e,
            })
          );
        } else {
          keys.push({
            title: element.fieldTitle,
            key: element.fieldKey,
          });
        }
      });
      console.log("title:", title);
      console.log("keys:", keys);
      let finished = false;

      for (let i = 0; i < keys.length; i++) {
        if (keys[i].key === "location_") {
          continue;
        }
        let value = this.$store.state.productPage.payload[keys[i].key];
        console.log(value);
        if (value == undefined) {
          console.log("payload key; ", keys[i].key);
          console.log("key title; ", keys[i].title);
          const canBeIgnored = ["活动特色", "优势认证", "餐饮标准"];
          if (canBeIgnored.indexOf(keys[i].title) != -1) {
            continue;
          }
          this.setModuleEmpty(keys[i].title);
          console.log(
            this.title,
            " returned after return_________________________________________________"
          );
          return;
        }
      }

      this.setModuleFinished();
      console.log(
        this.title,
        " set done_________________________________________________"
      );
    });
  },
  methods: {
    setModuleFinished() {
      this.$store.commit("productPage/updateModule", {
        moduleTitle: this.title,
        name: "",
        done: true,
        selected: false,
        empty: "",
      });
    },
    setModuleEmpty(title) {
      console.log("set empty ");
      console.log("this.titls:", this.title);
      console.log("empty.titls:", title);
      this.$store.commit("productPage/updateModule", {
        moduleTitle: this.title,
        name: title,
        done: false,
        selected: false,
        empty: title,
      });
    },
    setInputsTitle() {
      this.$data.fields = this.$data.fields.map((e, i) => ({
        fieldTitle: e[0],
        fieldPayloadKey: e[1],
      }));
    },
    test(e) {
      // console.log(this.$data)
      console.log('state payload" ', this.$store.state.productPage.payload);
      // console.log('state payload" ',this.$store.state.productPage)
    },
    checkFinished() {
      console.log(this.fields);
    },
    addstate() {
      let keys = ["product_name", "product_categories"];
      let value = [
        "aavvc",
        [
          { name: "测试1", selected: true, indd: 1 },
          { name: "测试3", selected: true, indd: 3 },
          { name: "删掉11", selected: true, indd: 11 },
          { name: "删掉13", selected: true, indd: 13 },
        ],
      ];
      for (let i = 0; i < keys.length; i++) {
        this.$store.commit("productPage/setPayload", {
          [keys[i]]: {
            value: value[i],
          },
        });
      }
    },
  },
  props: {
    moduleTitle: {
      type: String,
    },
  },
};
</script>
