<template>
  <div class="label">活动类型:</div>
  <div class="content">
    <!-- <select v-model="globalValue">
            <option value="" disabled selected style="display: none">请选择活动类型</option>
            <option value="游学">游学</option>
            <option value="研学">研学</option>
            <option value="夏令营">夏令营</option>
            <option value="营地活动">营地活动</option>
            <option value="主题活动">主题活动</option>
            <option value="公益实践">公益实践</option>
            <option value="赛事报名">赛事报名</option>
            <option value="其他">其他</option>
        </select> -->
    <SelectInput
      :List="selectList"
      :value="globalValue"
      @sendValue="changeType"
      title="请选择活动类型"
    ></SelectInput>
  </div>
  <div class="clear"></div>
</template>

<script>
import { useStore } from "vuex";
import { computed } from "@vue/runtime-core";
import Select from "@/pages/internal/views/providerTools/activityCreateForm/inputs/Select.vue";
import SelectInput from "../../../inputs/SelectInput.vue";
export default {
  name: "ActivityType",
  setup(props, context) {
    const store = useStore();
    const globalValue = computed({
      get() {
        let value = store.state.productPage.payload["activity_type"];
        if (value) return value.value;
        return "";
      },
      set(val) {
        let payload = {};
        payload["activity_type"] = { value: val };
        store.commit("productPage/setPayload", payload);
      },
    });
    const changeType = (e) => {
      globalValue._setter(e);
    };
    return {
      globalValue,
      changeType,
    };
  },
  data() {
    return {
      selectList: [
        { value: "游学", option: "游学" },
        { value: "研学", option: "研学" },
        { value: "夏令营", option: "夏令营" },
        { value: "营地活动", option: "营地活动" },
        { value: "主题活动", option: "主题活动" },
        { value: "公益实践", option: "公益实践" },
        { value: "赛事报名", option: "赛事报名" },
      ],
    };
  },
  methods: {},
  components: {
    Select,
    SelectInput,
  },
};
</script>

