<template>
  <field-structure :fieldTitle="fieldTitle">
    <div class="single-select">
      <!-- <select name="" id="" class="basic-input" v-model="globalValue">
                <option value="0" selected>请选择</option>
                <option :value="item.id" :key="index" v-for="(item,index) in showList">{{item.name}}</option>
            </select> -->
      <SelectInput2
        :List="selectList"
        :value="globalValue"
        @sendValue="changeType"
        title="请选择"
      ></SelectInput2>
      <div style="position: relative">
        <div
          class="img3"
          @mouseenter="ifShowPop = true"
          @mouseleave="ifShowPop = false"
        ></div>
        <pop-info-black :left="50" :top="-15" v-show="ifShowPop">
          您所选择的退改方案详情请见《预定退费须知》
        </pop-info-black>
      </div>
    </div>
    <!-- <textarea
      v-show="globalValue == 1"
      v-model="schemeDetail"
      class="scheme-detail-input"
      placeholder="请填写具体退改方案"
    /> -->
  </field-structure>
</template>

<script>
import { computed, ref, toRaw } from "@vue/runtime-core";
import { useStore } from "vuex";
import FieldStructure from "./FieldStructure.vue";
import SelectInput from "./SelectInput.vue";
import SelectInput2 from "./SelectInput2.vue";
import { $getRefundpolicyAll } from "@/pages/internal/network/providerTools/provider.js";
import PopInfoBlack from "@/pages/internal/views/providerTools/activityCreateForm/inputs/popInfoBlack";
export default {
  name: "SimpleTextXYZ",
  components: {
    FieldStructure,
    SelectInput,
    SelectInput2,
    PopInfoBlack,
  },
  props: {
    field: Object,
  },
  data() {
    return {
      showList: [],
      ifShowPop: false,
    };
  },
  computed: {
    selectList() {
      var arr = this.showList.map((item) => {
        var name = item.name;
        if (item.method.length !== 0) {
          name += ":";
          for (var i = 0; i < item.method.length; i++) {
            name += item.method[i].name;
            if (i == item.method.length - 1) {
              name += "。";
            } else name += ";";
          }
        }
        return {
          value: item.id,
          option: name,
        };
      });
      arr.splice(2, 0, {
        value: 0,
        option: "有条件退" + "\xa0\xa0\xa0" + ">",
      });
      console.log(arr);
      return arr;
      // return this.showList.map((item) => {
      //   return {
      //     value: item.id,
      //     option: item.name,
      //   };
      // });
    },
  },
  mounted() {
    const that = this;
    async function getRefundpolicyAll() {
      const res = await $getRefundpolicyAll({});
      console.log("refund", res);
      if (res.Code == 200) {
        that.showList = res.details;
      }
    }
    getRefundpolicyAll();
  },
  setup(props, context) {
    const store = useStore();
    const valueLength = ref(0);
    let { charLimit, fieldTitle, fieldKey, ins } = toRaw(props.field);
    console.log(charLimit);
    console.log(fieldTitle);
    console.log(fieldKey);
    console.log(ins);
    if (!charLimit) {
      charLimit = 20;
    }
    const schemeDetail = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        console.log(value);
        if (!value) {
          return "";
        }
        return value.content || "";
      },
      set(val) {
        let value = store.state.productPage.payload[fieldKey];
        store.commit("productPage/setPayload", {
          [fieldKey]: {
            ...value,
            content: val,
          },
        });
        store.commit("productPage/setPayload", {
          [`${fieldKey}_id`]: {
            ...value,
            content: val,
          },
        });
        console.log(store.state.productPage.payload[fieldKey]);
      },
    });
    const globalValue = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        console.log("退改方案____________________________________________");
        if (!value) {
          return 0;
        }
        store.commit("productPage/setPayload", {
          refundpolicy_id: {
            value: Number(value.value) || 0,
          },
        });
        return value.value;
      },
      set(val) {
        store.commit("productPage/setPayload", {
          [fieldKey]: {
            value: Number(val),
          },
        });
        store.commit("productPage/setPayload", {
          [`${fieldKey}_id`]: {
            value: Number(val),
          },
        });
      },
    });
    const changeType = (e) => {
      globalValue._setter(e);
    };
    return {
      globalValue,
      fieldTitle,
      fieldKey,
      valueLength,
      charLimit,
      ins,
      schemeDetail,
      changeType,
    };
  },
};
</script>

