<template>
  <div class="label">活动人数:</div>
  <div class="content">
    招收 &nbsp;<IntegerInput
      :value="globalValue"
      @valueChanged="change"
    />&nbsp; 人
  </div>
  <div class="clear"></div>
</template>

<script>
import { useStore } from "vuex";
import { computed } from "@vue/runtime-core";
import IntegerInput from "@/pages/internal/views/providerTools/activityCreateForm/inputs/IntegerInput";

export default {
  name: "PeopleLimit",
  setup(props, context) {
    const store = useStore();
    const globalValue = computed({
      get() {
        let value = store.state.productPage.payload["size_valid"];
        if (value) return value.value;
        return 1;
      },
      set(val) {
        let payload = {};
        payload["size_valid"] = { value: val };
        store.commit("productPage/setPayload", payload);
      },
    });
    return {
      globalValue,
    };
  },
  data() {
    return {};
  },
  methods: {
    change(val) {
      // console.log(val)
      this.globalValue = val;
    },
  },
  components: {
    IntegerInput,
  },
};
</script>

