<template>
  <div class="vid-upload">
    <div class="notes">
      *您应确保所上传图片视频素材不涉及侵权，所有图片视频为原创或已获得版权方的授权进行使用，涉及人脸漏出的商业应用应确保已获得肖像权授权等
    </div>
    <div class="vid-title">短视频</div>
    <div class="click-note">
      *请上传一段不超过2分钟，大小10M以内的短时视频，内容为行前说明或活动风采展示
    </div>
    <div class="middle">
      <div
        class="vid"
        v-show="!isShowVideo"
        @mouseover="isMaskShow"
        @mouseleave="isMaskShow"
      >
        <video class="vid" :src="videoUrl" controls></video>
        <div class="mask" v-show="maskShow">
          <div @click="sefi">重新上传</div>
          <div @click="deleteVideo">删除</div>
        </div>
      </div>

      <div class="click-vid" @click="sefi" v-show="isShowVideo">
        <input
          type="file"
          id="videoUpload"
          v-show="false"
          @change="onFileChange"
        />
      </div>
      <div class="fixedFullLoading" v-show="isLoading">
        <a-space>
          <a-spin size="large"></a-spin>
        </a-space>
      </div>
    </div>
  </div>
</template>

<script>
import { computed } from "@vue/runtime-core";
import { $uploadStreamMediaFile } from "../../../../network/common/common";
import { useStore } from "vuex";
export default {
  created() {
    // this.$toast.error('测试')
  },
  setup(props, context) {
    let fieldKey = "video_short";
    let store = useStore();
    const videoUrl = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        // 视频的地址有两种情况，一种是第一次渲染从后端读取的，返回的是字符串，需要自己切，第二种是自己上传的，在vuex中设置一个对象
        if (value) {
          if (typeof value.value === "string") {
            console.log("📕", value.value);
            const url = value.value.slice(9, value.value.length - 2);
            return url;
          } else {
            console.log("📕", value);
            return value.value.url;
          }
        } else return "";

        //   return JSON.parse(value.value).url
      },
      set(val) {},
    });
    return {
      videoUrl,
    };
  },
  data() {
    return {
      maskShow: false,
      originalVideoUrl: "",
      componentStatusCode: "100",
      targetData: "",
      isLoading: false,
    };
  },
  computed: {
    isShowVideo() {
      return this.videoUrl == "" || this.videoUrl?.url == "";
    },
  },
  methods: {
    isMaskShow(e) {
      let dom = e.target;
      if (e.type == "mouseover") {
        this.maskShow = true;
      } else {
        this.maskShow = false;
      }
    },
    deleteVideo() {
      this.setVideoUrl("");
    },
    sefi() {
      document.getElementById("videoUpload").click();
      // console.log('d')
    },
    setVideoUrl(e) {
      console.log(e);
      let val = { url: e };
      this.videoUrl = e;
      this.$store.commit("productPage/setPayload", {
        ["video_short"]: {
          value: val,
        },
      });
    },
    failed(e) {
      this.$toast.error("上传失败");
    },
    async onFileChange(e) {
      const file_input = document.getElementById("videoUpload");
      const files = e.target.files || e.dataTransfer.files;

      if (!files.length) return;

      if (!/video\/\w+/.test(files[0].type)) {
        this.$toast.error("文件格式不符要求", 240);
        file_input.value = "";
        return;
      }

      if (files[0].size / 1024 / 1024 > 10.0) {
        this.$toast.error("视频大小超过10M");
        file_input.value = "";
        return;
      }

      this.isLoading = true;

      const formData = new FormData();
      formData.append("image", files[0]);

      try {
        const response = await $uploadStreamMediaFile(formData);
        console.log(response);
        const { Code, msg } = response;
        if (Code === 200 && msg === "success") {
          const { details } = response;
          if (details.image_url) {
            this.$toast.success("上传成功");
            this.setVideoUrl(details.image_url);
          } else {
            this.failed();
          }
        } else {
          this.failed();
        }
        file_input.value = "";
        this.isLoading = false;
      } catch (err) {
        this.failed();
        file_input.value = "";
        this.isLoading = false;
      }
    },
  },
};
</script>

