<template>
  <div class="label">日程形式:</div>
  <div class="content">
    <!-- <select v-model="if_stay_out">
            <option value="" disabled selected style="display: none">请选择是否住宿</option>
            <option value="true">需要住宿</option>
            <option value="false">不需要</option>
        </select> -->
    <SelectInput
      :List="selectList"
      :value="if_stay_out"
      @sendValue="changeStay"
      title="请选择是否需要住宿"
    ></SelectInput>
    <SelectInput
      :List="selectActivityFromList"
      :value="activity_form"
      @sendValue="changeFrom"
      title="请选择参与形式"
    ></SelectInput>
    <!-- <select v-model="activity_form">
            <option value="" disabled selected style="display: none">请选择参与形式</option>
            <option value="亲子">亲子</option>
            <option value="独立">独立</option>
            <option value="单飞">单飞</option>
        </select> -->
    <div
      class="img"
      @mouseenter="ifShowPop = true"
      @mouseleave="ifShowPop = false"
    ></div>
    <pop-info-black :left="360" :top="-15" v-show="ifShowPop"
      >亲子：监护人全程陪同参加。 独立：儿童与青少年独立参加。
      单飞：亲子共同参加，但设有各自独立活动时间。</pop-info-black
    >
  </div>
  <div class="clear"></div>
</template>

<script>
import { useStore } from "vuex";
import { toRefs } from "vue";
import { computed } from "@vue/runtime-core";
import PopInfoBlack from "@/pages/internal/views/providerTools/activityCreateForm/inputs/popInfoBlack";
import SelectInput from "../../../inputs/SelectInput.vue";

export default {
  name: "ActivityForm",
  components: { PopInfoBlack, SelectInput },
  setup(props, context) {
    const store = useStore();
    const if_stay_out = computed({
      get() {
        let value = store.state.productPage.payload["stay_if"];
        if (value) return value.value;
        return "";
      },
      set(val) {
        let payload = {};
        payload["stay_if"] = { value: val };
        if (val == "true") {
          payload.stay_if.value = true;
        } else {
          payload.stay_if.value = false;
        }
        store.commit("productPage/setPayload", payload);
      },
    });
    const activity_form = computed({
      get() {
        let value = store.state.productPage.payload["activity_mode"];
        if (value) return value.value;
        return "";
      },
      set(val) {
        let payload = {};
        payload["activity_mode"] = { value: val };
        store.commit("productPage/setPayload", payload);
      },
    });
    const changeStay = (e) => {
      if_stay_out._setter(e);
    };
    const changeFrom = (e) => {
      activity_form._setter(e);
    };
    return {
      if_stay_out,
      activity_form,
      changeStay,
      changeFrom,
    };
  },
  data() {
    return {
      ifShowPop: false,
      selectList: [
        { value: true, option: "需要住宿" },
        { value: false, option: "不需要住宿" },
      ],
      selectActivityFromList: [
        { value: "亲子", option: "亲子" },
        { value: "单飞", option: "单飞" },
        { value: "独立", option: "独立" },
      ],
    };
  },
  methods: {
    test() {
      alert(1);
    },
  },
};
</script>

