<template>
  <field-structure :fieldTitle="fieldTitle">
    <div class="addables">
      <div class="addable" v-for="(v, i) in globalValue" :key="i">
        <div class="addable-title">{{ addableTitle }}{{ i + 1 }}</div>
        <img
          v-if="i != 0"
          @click="deleteAddable(i)"
          src="../Modules/3_Schedule/resource/delete.png"
          alt=""
        />
        <div class="addable-input with-char-limit">
          <input
            type="text"
            class="basic-input"
            v-model="v.title"
            :placeholder="tlpl"
          />
          <div class="char-limit">
            <span>{{ changeItem(v, i, "title") }}</span
            >/{{ charLimit }}
          </div>
        </div>
        <div class="addable-detail-title">
          {{ addableTitle }}{{ i + 1 }}详情{{
            addableTitle == "提纲章节" ? "(选填)" : ""
          }}
        </div>
        <div
          class="addable-input addable-input addable-detail-input with-char-limit"
        >
          <textarea
            :id="fieldKey"
            :placeholder="detailpl"
            class="ele-input basic-input"
            name="te"
            cols="30"
            rows="10"
            v-model="v.detail"
            :style="style"
          >
          </textarea>
          <div class="char-limit">
            <span>{{ changeItem(v, i, "detail") }}</span
            >/{{ detailedCharLimit }}
          </div>
        </div>
      </div>
      <div class="add-item" @click="addAddable">+</div>
    </div>
  </field-structure>
</template>

<script>
import { computed, reactive, ref, toRaw, toRef } from "@vue/runtime-core";
import { useStore } from "vuex";
import FieldStructure from "./FieldStructure.vue";
export default {
  name: "deleteAddable",
  components: {
    FieldStructure,
  },
  props: {
    field: Object,
  },
  setup(props, context) {
    // console.log(props)

    const store = useStore();

    const valueLength = ref(0);
    const {
      charLimit,
      fieldTitle,
      fieldKey,
      style,
      detailedCharLimit,
      addableTitle,
    } = toRaw(props.field);

    let { tlpl, detailpl } = toRaw(props.field);
    if (!tlpl) {
      tlpl = "请输入";
    }
    if (!detailpl) {
      detailpl = "请输入";
    }
    const globalValue = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        if (!value) {
          store.commit("productPage/setPayload", {
            [fieldKey]: {
              value: [
                {
                  title: "",
                  detail: "",
                },
              ],
            },
          });
          value = store.state.productPage.payload[fieldKey];
        }

        return value.value;
      },
      set(val) {
        console.log(val);
      },
    });
    const addAddable = () => {
      console.log("add");
      console.log(globalValue);
      let pre = store.state.productPage.payload[fieldKey];
      if (!pre) {
        pre = {
          value: [
            {
              title: "",
              detail: "",
            },
          ],
        };
      }
      pre = pre.value;
      pre.push({ title: "", detail: "" });
      store.commit("productPage/setPayload", {
        [fieldKey]: {
          value: pre,
        },
      });
    };
    const deleteAddable = (index) => {
      let addAbleList = globalValue.value;
      addAbleList.splice(index, 1);
      store.commit("productPage/setPayload", {
        [fieldKey]: {
          value: addAbleList,
        },
      });
    };
    return {
      globalValue,
      fieldTitle,
      fieldKey,
      valueLength,
      charLimit,
      detailedCharLimit,
      addableTitle,
      style,
      addAddable,
      deleteAddable,
      tlpl,
      detailpl,
    };
  },
  methods: {
    /**
     * 这个方法用作返回当前文本长度并监控是否超出范围
     * @param {object} value - 当前元素在数组中的value值
     * @param {number} index - 当前元素在数组中的索引
     * @param {string} type  - 当前这个函数处理的类型，保证不同表单的数据隔离
     */
    changeItem(value, index, type) {
      let { charLimit, detailedCharLimit } = this.field;
      // 维护一个类型和字符限制数的对象
      const charLimitObject = {
        title: charLimit,
        detail: detailedCharLimit,
      };
      if (
        !Object.keys(charLimitObject).some((item) => {
          return type == item;
        })
      ) {
        throw "注意changeItem的type参数";
      }
      let { length } = value[type];
      charLimit = charLimitObject[type];
      if (charLimit < length) {
        this.globalValue[index][type] = this.globalValue[index][type].substring(
          0,
          charLimit
        );
      }
      return length;
    },
  },
};
</script>

