<template>
  <div class="process-bar">
    <div
      @click="scrollTo(i)"
      v-for="(item, i) in list"
      :key="i"
      class="moduleName"
      :class="state === i ? 'active' : ''"
    >
      {{ item }}
    </div>
  </div>
</template>

<script>
export default {
  name: "ProcessBar",
  props: {
    // modules:{
    //     type:Array
    // }
  },
  mounted() {
    let e = document.getElementsByClassName("module");
    for (let i = 0; i < e.length; i++) {
      this.offsets[i] = e[i].offsetTop - 2;
    }
    let updateState = this.updateState;
    this.internal = setInterval(function () {
      updateState();
    }, 100);
  },
  data() {
    return {
      list: [
        "商品基本信息",
        "课程详情",
        "活动日程表",
        "保险类型",
        "商品价格",
        "商品详情",
        "开营须知",
        "购买须知",
      ],
      state: 0,
      offsets: [],
      internal: 0,
    };
  },
  computed: {},
  methods: {
    updateState() {
      let e = document.getElementsByClassName("module");
      for (let i = 0; i < e.length; i++) {
        this.offsets[i] = e[i].offsetTop - 2;
      }
      if (!document.getElementById("mainContent").scrollTop) return;
      let scrollNum = document.getElementById("mainContent").scrollTop;
      for (let i = this.offsets.length; i > 0; i--) {
        if (scrollNum >= this.offsets[i]) {
          this.state = i;
          return;
        }
      }
      this.state = 0;
    },
    scrollTo(val) {
      let e = document.getElementsByClassName("module");
      if (e[val]) {
        e[val].scrollIntoView();
        this.updateState();
      }
    },
  },
  beforeUnmount() {
    clearInterval(this.internal);
  },
};
</script>

<style>
.process-bar {
  width: 200px;
  position: sticky;
  top: 1px;
}

.moduleName {
  height: 32px;
  padding-left: 14px;
  line-height: 32px;
  font-size: 16px;
  color: #8a8a8a;
  font-family: "PingFang SC";
  border-left: 1px solid gainsboro;
}

.active {
  border-left: 2px solid #93d900;
  color: #93d900;
}
</style>
