<template>
  <div class="vid-upload">
    <div class="vid-title">
      {{ field.title }}
    </div>
    <div class="click-note">
      {{ field.note }}
    </div>
    <div class="middle imgsList">
      <div class="imgs" v-for="(v, i) in imgAr" :key="i">
        <div
          class="imgcover"
          :style="img_style"
          @mouseover="isShowItem"
          @mouseleave="isShowItem"
        >
          <div @click="deleteItemImg(i)">删除</div>
          <div @click="reUpload(i)">重新上传</div>
        </div>
        <img
          @mouseover="isShowItem"
          @mouseleave="isShowItem"
          class="ar-img"
          :style="img_style"
          :src="v.url"
          alt=""
        />
        <input type="text" :style="img_style" v-model="v.name" />
      </div>

      <div class="click-vid" :style="img_style" @click="sefi">
        <input
          type="file"
          :id="field.id"
          v-show="false"
          @change="onFileChange"
        />
      </div>
      <div class="fixedFullLoading" v-show="isLoading">
        <a-space>
          <a-spin size="large"></a-spin>
        </a-space>
      </div>
    </div>
  </div>
</template>

<script>
import { computed, toRaw } from "@vue/runtime-core";
import uuid from "uuid";
import Sortable from "sortablejs";
import { $uploadStreamMediaFile } from "../../../../network/common/common";
import {
  deleteFileNameSuffix,
  readImageFileToDetectRatio,
  uniqueArr,
} from "../../../../common/utils";
import { useStore } from "vuex";
export default {
  props: {
    field: {
      type: Object,
    },
  },
  setup(props, context) {
    const store = useStore();
    let url = "/media/%E6%A8%AA%E6%9D%BF%E9%A3%8E%E9%87%871_7LalOlA.jpg";

    let { fieldKey, img_style } = toRaw(props.field);
    if (!img_style) {
      img_style = {
        width: "180px",
        height: "120px",
      };
    }
    // console.log(fieldKey)
    const imgAr = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        if (!value) {
          return [];
        }
        return value.value;
      },
    });
    return {
      imgAr,
      img_style,
    };
  },
  data() {
    return {
      isLoading: false,
      reUploadIndex: null,
    };
  },
  methods: {
    // 根据鼠标事件决定是否图片的显示操作按钮
    isShowItem(e) {
      let dom = e.target;
      if (e.target.getAttribute("class") == "ar-img") {
        dom = e.target.previousSibling;
      }
      if (["imgcover", "ar-img"].indexOf(dom.getAttribute("class")) == -1) {
        return;
      }
      if (e.type == "mouseover") {
        dom.style.display = "flex";
      } else {
        dom.style.display = "none";
      }
    },
    washToSelect(selectedAr) {
      console.log("wash");
      let toDeleteIndex;
      selectedAr.forEach((element) => {
        toDeleteIndex = this.toSelectList.findIndex(
          (e) => e.id == element.feature_id
        );
        console.log("wash", toDeleteIndex);
        this.toSelectList.splice(toDeleteIndex, 1);
      });
    },
    deleteItemImg(index) {
      this.imgAr.splice(index, 1);
      const value = this.imgAr;
      let fieldKey = this.field.fieldKey;
      this.$store.commit("productPage/setPayload", {
        [fieldKey]: {
          value: value,
        },
      });
    },
    reUpload(index) {
      this.reUploadIndex = index;
      document.getElementById(this.field.id).click();
    },
    sefi() {
      let id = this.field.id;
      let arlength = toRaw(this.imgAr).length;
      if (arlength >= 5) {
        this.$toast.warn("已添加五张图片 ");
        return;
      }
      document.getElementById(id).click();
      console.log(id);
    },
    add_img(e) {
      console.log(e);
      console.log(this.field.fieldKey);
      let fieldKey = this.field.fieldKey;
      let value = this.$store.state.productPage.payload[fieldKey];
      if (value) {
        value = toRaw(value).value;
      } else {
        value = [];
      }
      console.log(value);
      // 通过判断reUploadIndex的值是否未null来决定是替换还是添加
      if (this.reUploadIndex !== null) {
        this.imgAr.splice(this.reUploadIndex, 1, e);
        value.splice(this.reUploadIndex, 1, e);
        this.reUploadIndex = null;
      } else {
        value.push(e);
      }
      this.$store.commit("productPage/setPayload", {
        [fieldKey]: {
          value: value,
        },
      });
    },
    async onFileChange(e) {
      const file_input = document.getElementById(this.field.id);
      let files = e.target.files || e.dataTransfer.files;
      //console.log(files[0]);
      //console.log(e.target.value);
      // 若e.target.files为空，则不继续往下进行
      if (!files.length) return;
      const originalpic = files[0];
      const ret = await this.$print.getPrintedFile(files[0]);
      files = [ret];
      // 判断图片格式是否为jpg格式【已经往input中添加accept属性】
      if (!/image\/jp(e)g/.test(files[0].type)) {
        this.$toast.error("文件格式不符要求");
        file_input.value = "";
        return;
      }

      // 判断图片大小是否小于等于2MB
      const size = originalpic.size / 1024; //单位：kb
      if (size > 2048) {
        this.$toast.error("图片大小超2MB");
        file_input.value = "";
        return;
      }

      try {
        const res = await readImageFileToDetectRatio(
          files[0],
          this.field.idealRatio
        );
        console.log("readImageFileToDetectRatio");
        if (res.code !== "1111") {
          this.$toast.error("图片长宽比例不符要求", 240);
          file_input.value = "";
          this.isLoading = false;
          console.log("ratio check return");
          return;
        }
      } catch (err) {
        this.$toast.error("图片读取失败", 240);
        file_input.value = "";
        this.isLoading = false;
        return;
      }

      this.isLoading = true;

      const formData = new FormData();
      formData.append("image", files[0]);
      try {
        const response = await $uploadStreamMediaFile(formData);
        const { Code, msg } = response;
        if (Code === 200 && msg === "success") {
          const { details } = response;
          if (details.image_url) {
            this.add_img({
              url: details.image_url,
              name: deleteFileNameSuffix(files[0].name),
            });
            this.$toast.success("图片上传成功", 240);
          } else {
            this.$toast.error("上传失败", 240);
          }
        } else {
          this.$toast.error("上传失败", 240);
        }

        file_input.value = "";
        setTimeout(() => {
          this.isLoading = false;
        }, 600);
      } catch (err) {
        this.$toast.error("上传失败", 240);
        file_input.value = "";
        this.isLoading = false;
      }
    },
  },
  computed: {},
  mounted() {
    const $ul = this.$el.querySelector(".imgsList");
    this.sortList = Sortable.create($ul, {
      handle: ".imgs",
      draggable: ".imgs",
      animation: 250,
      onUpdate: (event) => {
        const newIndex = event.newIndex;
        const oldIndex = event.oldIndex;
        const $li = $ul.children[newIndex];
        const $oldLi = $ul.children[oldIndex];
        // 先删除移动的节点
        $ul.removeChild($li);
        // 再插入移动的节点到原有节点，还原了移动的操作
        if (newIndex > oldIndex) {
          $ul.insertBefore($li, $oldLi);
        } else {
          $ul.insertBefore($li, $oldLi.nextSibling);
        }
        const item = this.imgAr[event.oldIndex];
        this.imgAr.splice(event.oldIndex, 1);
        this.imgAr.splice(event.newIndex, 0, item);
      },
    });
  },
  created() {
    // console.log(this.$props)
    // console.log(new Date().getTime())
  },
};
</script>

