<template>
  <field-structure :fieldTitle="field.fieldTitle" :required="true">
    <div class="click-to">
      <div class="outer-items">
        <div class="aaa">
          <div
            class="se-item greyed"
            v-for="(v, ind) in selectedItems"
            :key="ind"
            :class="'se-it'"
          >
            <img :src="v.certificate" alt="" class="cert-img" />
            <span>{{ idToCatName(v.feature_id) }}</span>
            <span class="closespan" @click="closespan(v)"> X </span>
          </div>
          <div class="add-item dir-baseInfo-certifiedFeatrue" @click="showHide">
            + 点击选择
          </div>
        </div>
      </div>

      <div
        class="to-select"
        v-show="showSelect"
        v-clickoutside:dir-baseInfo-certifiedFeatrue
      >
        <div class="se-title">
          {{ field.fieldTitle }}<span class="close" @click="cancel">X</span>
        </div>

        <div class="middle">
          <div class="cat-sel">
            <div class="pop-title">请选择认证类别</div>
            <select name="" id="" class="basic-input" v-model="toSelectItem">
              <option :value="0">请选择认证类型</option>
              <option v-for="(v, i) in toSelectList" :key="i" :value="v.id">
                {{ v.name }}
              </option>
            </select>
          </div>
          <div class="up-cet">
            <div class="pop-title">
              请上传相关证明材料&nbsp;&nbsp;&nbsp;
              <span class="pop-title-note inp">
                <span class="red">!</span
                >上传文件要清晰、四角齐全，格式为.jpg/.jpeg/.png
              </span>
            </div>
            <div class="upload-box">
              <div class="up">
                <div class="inp" @click="choose">
                  <div class="uppic"></div>
                  上传相关证明
                </div>
                <!-- <div class="so">支持扩展名： .jpeg .jpg</div> -->

                <div class="fixedFullLoading" v-show="isLoading">
                  <a-space>
                    <a-spin size="large"></a-spin>
                  </a-space>
                </div>
                <div class="uploaded" v-if="cacheState">
                  <img class="cert-img" :src="cacheFile.url" alt="" />
                  {{ cacheFile.name }}
                </div>
              </div>

              <input
                @change="onFileChange"
                type="file"
                name=""
                id="cert-file"
                v-show="false"
              />
            </div>
          </div>
        </div>

        <div class="bot">
          <div class="rightf">
            <div class="in"></div>
            <div class="cancel button" @click="cancel">取消</div>
            <div class="submit button" @click="confirm">确定</div>
          </div>
        </div>
      </div>
    </div>
    <slot> </slot>
  </field-structure>
</template>

<script>
import { reactive, toRaw } from "@vue/reactivity";

import FieldStructure from "./FieldStructure.vue";
import { computed, onMounted } from "@vue/runtime-core";
import store from "@/pages/internal/store/store";
import { $getEnvInfo } from "@/pages/internal/network/providerTools/activity";
import { $uploadStreamMediaFile } from "../../../../network/common/common";
import {
  deleteFileNameSuffix,
  readImageFileToDetectRatio,
  uniqueArr,
} from "../../../../common/utils";
export default {
  name: "SimpleText",
  components: {
    FieldStructure,
  },
  props: {
    field: Object,
  },
  computed: {
    showUpload() {
      console.log("toSelectItem", this.toSelectItem);
      return this.toSelectItem !== 0 && this.toSelectItem !== undefined;
    },
  },
  data() {
    return {
      defaultList: [],
      showSelect: false,
      toSelectList: [],
      toSelectItem: 0,
      fileInputId: "cert-file",
      isLoading: false,
      cacheState: false,
      cacheFile: {},
    };
  },
  computed: {
    selectedItems() {
      let glo =
        this.$store.state.productPage.payload[this.$props.field.fieldKey];
      if (!glo) {
        glo = {
          value: [],
        };
      }

      //清洗待选项
      toRaw(glo.value).forEach((element) => {
        let toDeleteIndex = this.toSelectList.findIndex(
          (e) => e.id == element.feature_id
        );

        if (toDeleteIndex > 0) {
          console.log("wash: ", toDeleteIndex);
          this.toSelectList.splice(toDeleteIndex, 1);
        }
      });

      return glo.value;
    },
  },

  async created() {
    try {
      const response1 = await $getEnvInfo();
      console.log("evn", response1);
      if (response1.Code === 200 && response1.msg === "success") {
        const {
          // activitymodes,
          // ages,
          categories,
          // cities,
          // days,
          features,
          // theme_related,
        } = response1.details;
        // console.log('features: ',features)
        this.defaultList = features;
        this.toSelectList = features.map((e) => e);
        this.toSelectList.push({ name: "无", id: undefined });
        //    console.log(this.toSelectList)
      } else {
        throw 1;
      }
    } catch (e) {
      console.log(e);
      console.log("基础配置信息获取失败，请尝试刷新页面");
    }
    this.$bus.on("pop", this.cancel);
  },
  beforeUnmount() {
    this.$bus.off("pop", this.cancel);
  },
  methods: {
    closespan(v) {
      // console.log(v)
      // 更新 global state
      let glo =
        this.$store.state.productPage.payload[this.$props.field.fieldKey];
      glo = toRaw(glo.value);
      // console.log('glo: ',glo)
      let toDeleteIndex = glo.findIndex((e) => e.feature_id == v.feature_id);
      // console.log("toDeleteIndex: ",toDeleteIndex)
      glo.splice(toDeleteIndex, 1);
      //  console.log('glo spliced: ',glo)
      this.$store.commit("productPage/setPayload", {
        [this.$props.field.fieldKey]: {
          value: glo,
        },
      });

      // 更新 toselect
      console.log(this.defaultList);
      let toAddCat = this.defaultList.find((e) => e.id == v.feature_id);
      console.log(toAddCat);
      this.toSelectList.push({ ...toAddCat });
      this.toSelectList.sort((a, b) => a.id - b.id);
    },
    idToCatName(id) {
      // console.log('de: ',this.defaultList)
      let res = toRaw(this.defaultList).find((e) => e.id == id);
      if (!res) {
        return "等待加载";
      }
      // console.log('22 res:',id)
      // console.log('22 res:',res)
      return res.name;
    },
    cancel() {
      this.cacheState = false;
      this.cacheFile = {};
      this.showSelect = false;
      this.toSelectItem = 0;
    },
    confirm() {
      let id = this.toSelectItem;
      let url = this.cacheFile.url;
      if (id == 0) {
        this.$toast.warn("请选择类别", 240);
        return;
      }
      if (!url) {
        this.$toast.warn("请重新上传图片", 240);
        return;
      }

      console.log(id, url);

      // 列表去掉选择的
      let toDeleteIndex = this.toSelectList.findIndex((e) => e.id == id);
      this.toSelectList.splice(toDeleteIndex, 1);

      // 提交payload
      let glo =
        this.$store.state.productPage.payload[this.$props.field.fieldKey];
      if (!glo) {
        glo = { value: [] };
      }
      let newglo = toRaw(glo.value);
      newglo.push({
        feature_id: id,
        certificate: url,
      });
      this.$store.commit("productPage/setPayload", {
        [this.$props.field.fieldKey]: {
          value: newglo,
        },
      });

      // 重置
      this.cancel();
    },
    showHide() {
      let statusquo = this.showSelect;
      if (statusquo) {
      } else {
      }
      this.showSelect = !statusquo;
    },

    choose() {
      document.getElementById("cert-file").click();
    },

    async onFileChange(e) {
      const file_input = document.getElementById("cert-file");
      let files = e.target.files || e.dataTransfer.files;
      //console.log(files[0]);
      //console.log(e.target.value);
      // 若e.target.files为空，则不继续往下进行
      if (!files.length) return;
      const originalpic = files[0];
      const ret = await this.$print.getPrintedFile(files[0]);
      files = [ret];
      // 判断图片格式是否为jpg格式【已经往input中添加accept属性】
      if (!/image\/jp(e)g/.test(files[0].type)) {
        this.$toast.error("文件格式不符要求");
        file_input.value = "";
        return;
      }

      // 判断图片大小是否小于等于2MB
      const size = originalpic.size / 1024; //单位：kb
      if (size > 2048) {
        this.$toast.error("图片大小超2MB");
        file_input.value = "";
        return;
      }

      this.isLoading = true;

      const formData = new FormData();
      formData.append("image", files[0]);
      try {
        const response = await $uploadStreamMediaFile(formData);
        console.log(response);
        const { Code, msg } = response;
        if (Code === 200 && msg === "success") {
          const { details } = response;
          if (details.image_url) {
            this.cacheState = true;
            this.cacheFile = {
              name: files[0].name,
              url: details.image_url,
            };

            this.$toast.success("图片上传成功", 240);
          } else {
            this.$toast.error("上传失败1", 240);
          }
        } else {
          this.$toast.error("上传失败2", 240);
        }

        file_input.value = "";
        setTimeout(() => {
          this.isLoading = false;
        }, 600);
      } catch (err) {
        console.log(err);
        this.$toast.error("上传失败3", 240);
        file_input.value = "";
        this.isLoading = false;
      }
    },
  },
};
</script>

