<template>
  <field-structure :fieldTitle="fieldTitle">
    <div class="camera-crew">
      <div class="note">
        *请填写是否有专职拍摄人员，拍摄人员数量，拍摄设备型号；无内容可留空
      </div>
      <div class="crew-detail">
        <!-- <select 
                name="" class="basic-input has-camera" id="" placeholder="sads"
                v-model="needed">
                    <option :value="undefined">选择是否有专职拍摄人员</option>
                    <option :value="true">有</option>
                    <option :value="false">无</option>
                </select> -->
        <SelectInput
          :List="selectList"
          :value="needed"
          @sendValue="changeType"
          title="是否有专职拍摄人员"
        ></SelectInput>
        <span v-show="needed">
          有<input
            type="number"
            class="basic-input cameras"
            name=""
            id=""
            v-model="size"
          />个拍摄人员
        </span>
      </div>
      <div v-show="needed" class="decices">
        使用<input
          type="text"
          name=""
          id=""
          class="basic-input devices"
          placeholder="请填写拍摄设备型号"
          v-model="device"
        />拍摄
      </div>
    </div>
  </field-structure>
</template>

<script>
import { computed, ref, toRaw } from "@vue/runtime-core";
import { useStore } from "vuex";
import FieldStructure from "./FieldStructure.vue";
import SelectInput from "./SelectInput.vue";
export default {
  name: "SimpleText",
  components: {
    FieldStructure,
    SelectInput,
  },
  props: {
    field: Object,
  },
  setup(props, context) {
    // console.log(props)
    const store = useStore();
    const valueLength = ref(0);
    const { charLimit, fieldTitle, fieldKey, style } = toRaw(props.field);

    // console.log('style: ',style)
    console.log(fieldKey);
    store.commit("productPage/setPayload", {
      [fieldKey]: {
        value: {
          needed: false,
          size: 0,
          device: "",
        },
      },
    });
    const needed = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        if (!value) {
          return undefined;
        }
        return value.value.needed;
      },
      set(val) {
        updateKey(val);
      },
    });
    const size = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        if (!value) {
          return 0;
        }
        return value.value.size;
      },
      set(val) {
        updateKey(val);
      },
    });
    const device = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        if (!value) {
          return false;
        }
        return value.value.device;
      },
      set(val) {
        updateKey(val);
      },
    });

    const updateKey = (val) => {
      let type = typeof val;
      let value = store.state.productPage.payload[fieldKey];
      if (!value) {
        value = { value: {} };
      }
      value = toRaw(value.value);
      if (type == "number") {
        value.size = val;
      } else if (type == "string") {
        value.device = val;
      } else {
        value.needed = val;
      }
      store.commit("productPage/setPayload", {
        [fieldKey]: {
          value,
        },
      });
    };
    const changeType = (e) => {
      needed._setter(e);
    };
    return {
      changeType,
      fieldTitle,
      fieldKey,
      valueLength,
      charLimit,
      style,
      needed,
      size,
      device,
    };
  },
  data() {
    return {
      selectList: [
        { value: true, option: "有" },
        { value: false, option: "无" },
      ],
    };
  },
};
</script>

<style lang="less" scoped>
.note {
  margin-bottom: 10px;
  color: #a6a6a6;
}
.has-camera {
  margin-right: 30px;
}
.cameras {
  width: 100px;
  padding-right: 10px;
  margin: 0 5px;
}
.devices {
  width: 250px;
  margin: 0 5px;
}
.camera-crew div {
  margin-bottom: 20px;
}
</style>
