<template>
  <field-structure :fieldTitle="fieldTitle" v-show="isStay">
    <div class="flex">
      <textarea
        :id="fieldKey"
        class="ele-input"
        name="te"
        cols="30"
        rows="7"
        v-model="globalValue"
        :placeholder="pl"
        :style="style"
      >
      </textarea>
      <div class="char-limit">
        <span>{{ valueLength }}</span
        >/{{ charLimit }}
      </div>
    </div>
  </field-structure>
</template>

<script>
/* eslint-disable */
import { computed, ref, toRaw } from "@vue/runtime-core";
import { useStore } from "vuex";
import FieldStructure from "./FieldStructure.vue";
export default {
  name: "SimpleText",
  components: {
    FieldStructure,
  },
  props: {
    field: Object,
  },
  computed: {
    // 根据是否选择了住宿来确定是否渲染这部分内容
    isStay() {
      if (this.fieldKey != "accommodation_management") {
        return true;
      }
      if (this.$store.state.productPage.payload?.stay_if?.value) {
        return true;
      }
      this.globalValue = " ";
      return false;
    },
  },
  setup(props, context) {
    const store = useStore();
    const valueLength = ref(0);
    let { charLimit, fieldTitle, fieldKey, style, pl } = toRaw(props.field);

    if (!pl) {
      pl = "请输入";
    }

    // console.log('style: ',style)
    const globalValue = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        if (!value) {
          return "";
        }
        return value.value;
      },
      set(val) {
        updateKey(val);
      },
    });
    const updateKey = (val) => {
      if (val.length > charLimit) {
        // console.log('chaole')
        val = val.substring(0, charLimit);
      }
      store.commit("productPage/setPayload", {
        [fieldKey]: {
          value: val,
        },
      });

      updateLength(val.length);
    };
    const updateLength = function (l) {
      valueLength.value = l;
    };
    return {
      globalValue,
      fieldTitle,
      fieldKey,
      valueLength,
      charLimit,
      pl,
      style,
    };
  },
};
</script>

