<template>
  <div class="label">难易度:</div>
  <div class="content">
    <div
      v-for="(index, i) in 5"
      :key="i"
      class="star"
      :class="difficulty >= index ? 'actived' : 'unactived'"
      @click="changeDifficulty(index)"
    ></div>
    <div class="word">{{ difficulty }} 星</div>
    <div
      class="img"
      @mouseenter="ifShowPop = true"
      @mouseleave="ifShowPop = false"
    ></div>
    <pop-info-black :left="340" :top="-10" v-show="ifShowPop"
      >1星无门槛，2星为入门级，3星为需要一定经验，4星为有一定专业背景，5星为需达到考核标准</pop-info-black
    >
  </div>
  <div class="clear"></div>
</template>

<script>
import { useStore } from "vuex";
import { toRefs } from "vue";
import { computed } from "@vue/runtime-core";
import PopInfoBlack from "@/pages/internal/views/providerTools/activityCreateForm/inputs/popInfoBlack";

export default {
  name: "DifficultySelect",
  components: { PopInfoBlack },
  props: {
    field: Object,
  },
  setup(props, context) {
    const store = useStore();
    const difficulty = computed({
      get() {
        let entity = store.state.productPage.payload["difficulty"];
        if (entity) return entity.value;
        return 0;
      },
      set(val) {
        let payload = {};
        payload["difficulty"] = { value: val };
        store.commit("productPage/setPayload", payload);
      },
    });
    return {
      difficulty,
    };
  },
  data() {
    return {
      ifShowPop: false,
    };
  },
  methods: {
    changeDifficulty(index) {
      this.difficulty = Number(index);
    },
  },
};
</script>

<style scoped>
.star {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin-right: 10px;
  background-size: 32px 32px;
}
.actived {
  background-image: url("actived.png");
}
.unactived {
  background-image: url("unactived.png");
}
.word {
  display: inline-block;
  position: relative;
  top: -8px;
  font-size: 1.5em;
}
.img {
  display: inline-block;
  background-image: url("question_mark.png");
  background-size: 15px 15px;
  width: 15px;
  height: 15px;
  margin-left: 20px;
  margin-bottom: 10px;
}
</style>
