<template>
  <div class="wrap">
    <div class="click-to">
      <div class="outer-items">
        <div class="aaa">
          <div class="add-item">
            <div
              class="se-item"
              v-for="(item, i) in selectList"
              :key="i"
              :class="item.selected ? 'se-it' : 'hidden'"
            >
              {{ item.name }}
              &nbsp;
              <span
                class="close"
                style="color: gainsboro"
                @click="uncheck(item)"
                v-show="item.value !== '中文' && item.value !== '普通话'"
                >X</span
              >
            </div>
            <div class="bySelf" @click="this.showSelect = !this.showSelect">
              自定义 <img src="./resource/down.png" alt="" />
            </div>
          </div>
        </div>
      </div>

      <div class="to-select" v-show="showSelect">
        <div class="se-title">
          支持语言 (可多选)<span class="close" @click="this.showSelect = false"
            >X</span
          >
        </div>
        <div class="middle">
          <div
            class="se-item"
            v-for="(item, i) in selectList"
            :key="i"
            :class="item.selected ? 'se-it' : ''"
            @click="changeStatus(item)"
          >
            {{ item.name }}
          </div>
        </div>
        <div class="bot">
          <div class="rightf">
            <div class="in">已选{{ childvalue.length }}项</div>
            <div class="button" @click="showCustom = true">自定义</div>
            <div class="submit button" @click="confirm">确定</div>
          </div>
        </div>
      </div>

      <div class="custom" v-show="showCustom">
        <div class="se-title">
          自定义支持语种<span class="close" @click="showCustom = false">X</span>
        </div>
        <input
          type="text"
          placeholder="请输入自定义支持语种"
          v-model="custom"
        />
        <div class="bot">
          <div class="rightf">
            <div class="button" @click="showCustom = false">取消</div>
            <div class="submit button" @click="addCustom">确定</div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div class="blacken" v-show="showSelect"></div>
</template>

<script>
export default {
  name: "MultipleSelect",
  props: {
    value: {
      default: function () {
        return [];
      },
      type: Array,
    },
    fieldTitle: {
      default: "",
      type: String,
    },
    options: {
      default: function () {
        return [];
      },
      type: Array,
    },
  },
  created() {
    this.isCreating = true;
    for (let i = 0; i < this.options.length; i++) {
      this.selectList[this.options[i].value] = {
        name: this.options[i].name,
        value: this.options[i].value,
        selected: false,
      };
    }

    this.updateSelectList();
    this.isCreating = false;
  },
  data() {
    return {
      showSelect: false,
      selectList: {},
      childvalue: this.value,
      showCustom: false,
      custom: "",
      isCreating: false,
    };
  },
  methods: {
    uncheck(item) {
      this.selectList[item.value].selected = false;
    },
    close() {
      this.showSelect = false;
    },
    changeStatus(e) {
      if (e.value === "中文" || e.value === "普通话") return;
      let status = e.selected;
      this.selectList[e.value].selected = !status;
    },
    confirm() {
      this.close();
    },
    updateSelectList() {
      for (let i = 0; i < this.childvalue.length; i++) {
        if (!this.selectList[this.childvalue[i]]) {
          this.selectList[this.childvalue[i]] = {
            name: this.childvalue[i],
            value: this.childvalue[i],
            selected: true,
          };
        } else {
          this.selectList[this.childvalue[i]].selected = true;
        }
      }
      console.log(this.value);
    },
    updateValue() {
      let value = [];
      let selectList = this.selectList;
      Object.keys(this.selectList).forEach(function (e) {
        if (selectList[e].selected) {
          value.push(selectList[e].value);
        }
      });
      this.childvalue = value;
    },
    addCustom() {
      if (this.selectList[this.custom] === undefined)
        this.selectList[this.custom] = {
          name: this.custom,
          value: this.custom,
          selected: true,
        };
      this.showCustom = false;
    },
  },
  watch: {
    value: {
      handler(newVal) {
        this.childvalue = newVal;
        this.updateSelectList();
      },
      deep: true,
    },
    childvalue: {
      handler(newVal) {
        this.$emit("valueChanged", newVal);
      },
      deep: true,
    },
    selectList: {
      handler(newVal) {
        if (this.isCreating) return;
        this.updateValue();
      },
      deep: true,
    },
  },
};
</script>

