<template>
  <field-structure :fieldTitle="fieldTitle">
    <div class="text-input" :style="{ marginLeft: field.margin + 'px' }">
      <input
        :id="fieldKey"
        :style="{ width: inputWidth + 'px' }"
        class="ele-input"
        type="text"
        v-model="globalValue"
        placeholder="请输入"
      />
      <div class="char-limit">
        <span>{{ valueLength }}</span
        >/{{ charLimit }}
      </div>
      <div class="ins" v-if="ins">{{ ins }}</div>
    </div>
  </field-structure>
</template>

<script>
import { computed, ref, toRaw, getCurrentInstance } from "@vue/runtime-core";
import { useStore } from "vuex";
import FieldStructure from "./FieldStructure.vue";
export default {
  name: "SimpleText",
  components: {
    FieldStructure,
  },
  props: {
    field: Object,
  },
  setup(props, context) {
    const { proxy } = getCurrentInstance();
    const store = useStore();
    const valueLength = ref(0);
    console.log(props.field);
    let { charLimit, fieldTitle, fieldKey, ins, inputWidth } = toRaw(
      props.field
    );
    if (!charLimit) {
      charLimit = 20;
    }
    if (!inputWidth) {
      inputWidth = 648;
    }
    const globalValue = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        if (!value) {
          return "";
        }
        return value.value;
      },
      set(val) {
        updateKey(val);
      },
    });

    const updateKey = (val) => {
      if (val.length > charLimit) {
        proxy.$message.warn("好像超出字符限制了哦", 5);
        val = val.substring(0, charLimit);
      }
      store.commit("productPage/setPayload", {
        [fieldKey]: {
          value: val,
        },
      });

      updateLength(val.length);
    };
    const updateLength = function (l) {
      valueLength.value = l;
    };
    return {
      globalValue,
      fieldTitle,
      fieldKey,
      valueLength,
      charLimit,
      ins,
      inputWidth,
    };
  },
};
</script>

