  methods: {
    async loadCertainProduct() {
      const id = this.$route.params.id;
      console.log("product id : ", id);

      if (id == undefined) {
        console.log("清空");
        this.$store.commit("productPage/deletePayload", "");
        return;
      }

      let product_id = Number(id);
      let res2;
      try {
        res2 = await $getProductByID({ id: product_id });
        console.log("📕", res2);
        console.log("get product by id: ", product_id, " : ", res2);
        if (res2.Code === 200 && res2.msg === "success") {
          if (
            res2.details.provider !==
            Number(window.localStorage.getItem("providerID"))
          ) {
            await this.$router.replace("/tools/provider/activity/update");
            return;
          }
          console.log("📕", res2);
          this.$store.commit(
            "productPage/setStatus",
            Number(res2.details.status)
          );
          // 修改state
          this.$store.commit(
            "productPage/setStatus",
            Number(res2.details.status)
          );
          let { details } = res2;
          details.categories = details.categories.map((e) => {
            return e.category;
          });
          const daysDiff = (date, date2) =>
            Math.ceil(Math.abs(date - date2) / 86400000);
          details.activity_days = daysDiff(
            new Date(details.activity_start_time),
            new Date(details.activity_end_time)
          );
          this.$store.commit("productPage/Initial", details);
          for (let i in details) {
            console.log(i), ": ";
            console.log();
            console.log(details[i]);
            this.$store.commit("productPage/setPayload", {
              [i]: {
                value: details[i],
              },
            });
          }
        } else {
          this.$toast.error("获取数据失败");
          this.isLoading = false;
          return;
        }
      } catch (err) {
        this.$toast.error("获取数据失败");
        console.log(err);
        this.isLoading = false;
        return;
      }
    },
    setModuleStaus() {
      // this.$store.commit("productPage/updateModuleStatus", [
