<template>
  <field-structure :fieldTitle="fieldTitle">
    <div class="uneditable">
      <div class="basic-input" :style="style">
        {{ note }}
      </div>
    </div>
  </field-structure>
</template>

<script>
import { computed, reactive, ref, toRaw } from "@vue/runtime-core";
import { useStore } from "vuex";
import FieldStructure from "./FieldStructure.vue";
export default {
  ewsdddddname: "InsSelect",
  components: {
    FieldStructure,
  },
  props: {
    field: Object,
  },
  setup(props, context) {
    // console.log(props)
    const store = useStore();
    const { fieldTitle, fieldKey, note, style } = toRaw(props.field);

    store.commit("productPage/setPayload", {
      [fieldKey]: {
        value: note,
      },
    });

    return {
      fieldTitle,
      fieldKey,
      note,
      style,
    };
  },
  methods: {},
};
</script>

<style lang="less" scoped>
.basic-input {
  white-space: pre-wrap;
}
</style>
