} from '@/pages/internal/network/providerTools/activity'
import { toRaw } from '@vue/reactivity'
import Toast from '@/pages/internal/components/common/toast/Toast'
import hashVal from '@/pages/internal/views/providerTools/activityCreateForm/components/hash'
export default {
  watch: {
    '$store.state.productPage.payload': {
      handler(val) {
        if (this.isObjectValueEqual(this.$store.state.productPage.init, val)) {
          this.changed = false
        } else {
          this.changed = true
        }
      },
      deep: true
    }
  },
  props: {
    // modules:{
    //     type:Array
    // }
  },
  data() {
    return {
      status: Number(this.$store.state.productPage.status),
      changed: false,
      showAgreement: true,
      isSale: false
    }
  },
  mounted() {},
  activated() {
    this.stateManager()
  },
  computed: {
    SaleState() {
      return !this.isSale ? '立即开售' : '已开售'
    }
  },
  methods: {
    stateManager() {
      // 获取所有表单元素
      let formNode = [
        ...document.getElementsByTagName('input'),
        ...document.getElementsByTagName('textarea'),
        ...document.getElementsByTagName('select')
      ]
      let productDetailNode = Array.from(
        document.getElementsByClassName('productdetail')
      )[0]
      // 判断是否需要让表单不可修改
      const statusFlag = Boolean(this.status == 0 || this.status == 5200)
      formNode.forEach(e => {
        e.disabled = !statusFlag
      })
      // 保证底部的特殊条件退订不可被修改
      const last = document.getElementsByClassName('last')[0]
      last.disabled = true
      // 利用事件捕获使自定义的组件失效
      productDetailNode.removeEventListener('click', this.notToChange, true)
      if (!statusFlag) {
        console.log('status', statusFlag)
        productDetailNode.addEventListener('click', this.notToChange, true)
      }
    },
    // 所有表单变得不可修改的函数
    notToChange(e) {
      // 保证底部的按钮可以使用
      let classList = e.target.getAttribute('class').split(' ')
      console.log(classList.indexOf('button'))
      if (classList.indexOf('button') != -1) {
        return true
      }
      // 阻止事件捕获，屏蔽表单中的点击事件
      this.$toast.warn('核保后不能修改')
      e.stopPropagation()
      return false
    },
    isObjectValueEqual(aObj, bObj) {
      if (!aObj || !bObj) {
        return false
      }
      const aProps = Object.keys(aObj)
      const bProps = Object.keys(bObj)
      if (aProps.length !== bProps.length) {
        return false
      }
      for (const i of bProps) {
        const propA = aObj[i]
        const propB = bObj[i]
        if (typeof propA === 'object') {
          if (this.isObjectValueEqual(propA, propB)) {
            // return true     这里不能return,后面的对象还没判断
          } else {
            //console.log(JSON.stringify(propA),JSON.stringify(propB))
            return false
          }
        } else if (propA !== propB) {
          //console.log(propA,propB)
          return false
        }
      }
      return true
    },
    setall() {
      let value = toRaw(this.$store.state.productPage.payload)
      console.log('all' + value)
      for (let i in value) {
        this.$store.commit('productPage/setPayload', {
          [i]: {
            value: 'set all'
          }
        })
      }
    },
    back() {
      this.$router.go(-1)
    },
    async check() {
      this.patchState()
      this.$bus.$emit('showKeys', '')
      let moduleStatus = this.$store.state.productPage.moduleStatus
      console.log('check status: ', toRaw(moduleStatus))
      let modules = toRaw(moduleStatus)

      console.log(moduleStatus)
      console.log(modules)
      // 获取每个模块el
      let e = document.getElementsByClassName('module')
      console.log('store', this.$store.state.productPage.payload)
      // 忽略选填内容
      const canBeIgnored = ['活动特色', '优势认证', '餐饮标准']
      for (let i in modules) {
        console.log(modules[i])
        if (modules[i].empty != '') {
          if (canBeIgnored.indexOf(modules[i].empty) != -1) {
            continue
          }
          this.$toast.warn(
            i + '模块中的' + modules[i].empty + '未填写！',
            0,
            500,
            3000
          )
          return
        }
      }
      const mustHaving = new Map([
        ['services', '服务'],
        ['outline', '教学提纲'],
        ['team', '团队配置'],
        ['price_include', '价格说明'],
        ['price_exclude', '价格说明'],
        ['price_insurance_detail', '价格说明'],
        ['video_short', '短视频']
      ])
      const RowObject = toRaw(this.$store.state.productPage.payload)
      console.log(RowObject)
      let isPassSave = null
      mustHaving.forEach((val, key) => {
        if (
          RowObject[key] === undefined ||
          RowObject[key]?.value === '' ||
          RowObject[key]?.value[0]?.title == '' ||
          RowObject[key]?.value?.url == ''
        ) {
          isPassSave = { val }
        }
      })
      if (isPassSave) {
        if (isPassSave.val == '价格说明') {
          this.$toast.warn(
            '购买须知模块中的' + isPassSave.val + '未填写！',
            0,
            500,
            3000
          )
        } else {
          this.$toast.warn(
            '商品详情模块中的' + isPassSave.val + '未填写！',
            0,
            500,
            3000
          )
        }
        return
      }
      // 检验日期的合法性
      const DateStore = this.$store.state.productPage.payload
      console.log(DateStore)
      let product_launching_time = new Date(
        DateStore['product_launching_time']?.value
      ).getTime()
      let product_expiration_time = new Date(
        DateStore['product_expiration_time']?.value
      ).getTime()
      let activity_start_time = new Date(
        DateStore['activity_start_time']?.value
      ).getTime()
      let activity_end_time = new Date(
        DateStore['activity_end_time']?.value
      ).getTime()
      const DateList = new Map([
        ['product_launching_time', '活动起售时间'],
        ['product_expiration_time', '活动停售时间'],
        ['activity_start_time', '活动开始时间'],
        ['activity_end_time', '活动结束时间']
      ])
      let timeFlag = false
      DateList.forEach((val, key) => {
        if (DateStore[key] === undefined) {
          this.$toast.warn(
            '商品基本信息模块中的' + val + '未填写！',
            0,
            500,
            3000
          )
          timeFlag = true
        }
      })
      if (timeFlag) {
        return
      }

      //模块七 合法性
      // 匹配手机号合法性
      let emergencyContacts =
        this.$store.state.productPage.payload['emergency_contacts'].value
      const phoneExp =
        /^(((\+86)|(\+86-))|((86)|(86\-))|((0086)|(0086\-)))?(1[3|4|5|7|8|9]\d{9}$)/
      const fixedPhoneExp = /^(0\d{2,3}\-)?([2-9]\d{6,7})+(\-\d{1,6})?$/
      for (let contactIndex in emergencyContacts) {
        let { phone } = emergencyContacts[contactIndex]
        console.log('phone', phoneExp.test(phone))
        if (!phoneExp.test(phone) && !fixedPhoneExp.test(phone)) {
          this.$toast.warn(
            `请检查紧急联系人${Number(contactIndex) + 1}电话是否合法`,
            0,
            500,
            3000
          )
          return
        }
      }
      // 验证是否同意条款
      // let agreed=document.getElementById("agreed").checked
      // console.log(agreed,document.getElementById("agreed"))
      // if(!agreed){
      //     this.$toast.warn(`请阅读并同意特殊条件退订机制`,0,500,3000)
      //     return
      // }
      var activity_time_location =
        this.$store.state.productPage.payload['activity_time_location'].value
      if (!activity_time_location.start_time)
        activity_time_location.start_time = new Date().toISOString()
      if (!activity_time_location.end_time)
        activity_time_location.end_time = new Date().toISOString()
      if (!activity_time_location.end_location)
        activity_time_location.end_location = ''
      if (!activity_time_location.end_location_detailed)
        activity_time_location.end_location_detailed = ''
      if (!activity_time_location.start_location)
        activity_time_location.start_location = ''
      if (!activity_time_location.start_location_detailed)
        activity_time_location.start_location_detailed = ''
      this.$store.commit('productPage/setPayload', {
        ['activity_time_location']: {
          value: activity_time_location
        }
      })
      this.subMitNewProd()
    },
    patchState() {
      console.log('📕', this.$store)
      this.$store.commit('productPage/setPayload', {
        ['group_contact']: {
          value: '报名当日，将有营探专属客服邀您入群'
        }
      })
      this.$store.commit('productPage/setPayload', {
        ['cautions']: {
