      })
    },
    async subMitNewProd(isCopy = false, isSave = false) {
      let deployData = {}
      let keys = toRaw(this.$store.state.productPage.payload)
      for (let i in keys) {
        deployData[i] = keys[i].value
      }
      const canBeIgnored = [
        'product_certified_features',
        'product_general_features'
      ]
      canBeIgnored.forEach(item => {
        if (deployData[item] === undefined) {
          deployData[item] = []
        }
      })
      deployData.provider_id = Number(window.localStorage.getItem('providerID'))
      const routerList = this.$router.currentRoute.value.fullPath.split('/')
      let operateState = 4200
      if (routerList[routerList.length - 1] != 'create') {
        operateState = 4300
      }
      // 复制产品的逻辑，删除id重新申请
      if (isCopy) {
        console.log(deployData)
        let { id, ...rest } = deployData
        deployData = rest
        console.log(deployData)
        operateState = 4200
      }
      let reason = isSave ? '保存草稿' : '创建商品'
      console.log('📕', deployData)
      const requestPayload = {
        user: {
          id: Number(window.localStorage.getItem('userID'))
        },
        version: '1.0.0',
        create_reason: reason,
        work_line_id: 1400,
        work_operation: operateState,
        content: {
          ...deployData
        }
      }
      let res = await $productCreate(requestPayload)
      if (res.Code == 200) {
        this.$toast.success('提交成功 等待审核')
      }
    },

    test(e) {
      // console.log(this.$data)
      console.log('state payload" ')
      let a = toRaw(this.$store.state.productPage.payload)
      console.log('state payload" ', a)
    },
    checkFinished() {
      console.log(this.fields)
    },
    saveState() {
      let state = this.$store.state.productPage

      state = toRaw(state)

      window.localStorage.setItem('state', JSON.stringify(state))
      this.$toast.success('保存成功', 240)
    },
    loadState() {
      let state = window.localStorage.getItem('state')
      state = JSON.parse(state)
      this.$store.commit('productPage/loadCache', state)
      this.$toast.success('加载成功', 240)
    },
