      }
    },
    async apply() {
      let res = await $submitProductToUnderwrite(
        '申请核保',
        this.$store.state.productPage.id
      )
      if (res.Code == 200) {
        this.$toast.success('提交成功')
        // 提交核保后修改状态并让表单无法被修改
        this.status = 5300
        this.stateManager()
      } else {
        this.$toast.warn('提交失败')
      }
    },
    async launch(type) {
      let res = await $submitLaunchProductRequestTicket(
        '商品上架',
        this.$store.state.productPage.id
      )
      if (res.Code == 200) {
        this.$toast.success('提交成功')
        if (type) {
          this.isSale = true
          return
        }
        this.status = 5700
      } else {
        this.$toast.warn('提交失败')
      }
    },
    duplicateWare() {
      this.saveState()
      window.isCopy = true
      this.$router
        .push('/tools/provider/activity/update?ProviderPage=2')
        .then(() => {})
    },
    async deleteWare() {
      let requestPayload = {
        user: {
          id: Number(window.localStorage.getItem('userID'))
        },
        version: '1.0.0',
        create_reason: 'TESTING',
        work_line_id: 1400,
        work_operation: 4800,
        content: {
          id: this.$store.state.productPage.id //product id
