<template>
  <field-structure :fieldTitle="fieldTitle">
    <div class="text-input">
      <slot name="before_number"></slot>
      <input
        :id="fieldKey"
        :style="style"
        class="ele-input"
        type="number"
        v-model="globalValue"
        :placeholder="pl"
      />
      <slot name="after_number"></slot>
      <div class="ins">{{ ins }}</div>
    </div>
  </field-structure>
</template>

<script>
import {
  computed,
  getCurrentInstance,
  getCurrentScope,
  ref,
  toRaw,
} from "@vue/runtime-core";
import { useStore } from "vuex";
import FieldStructure from "./FieldStructure.vue";
export default {
  name: "SimpleText",
  components: {
    FieldStructure,
  },
  props: {
    field: Object,
  },
  setup(props, context) {
    const { proxy } = getCurrentInstance();

    const store = useStore();

    const {
      fieldTitle,
      fieldKey,
      ins,
      pl,
      style,
      multiple = 1,
    } = toRaw(props.field);

    const globalValue = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey];
        if (!value) {
          return "";
        }
        console.log(multiple);
        return Number(value.value / multiple);
      },
      set(val) {
        // 为props添加一个字段canBeNegative来判断是否可以为负数
        // 若无这个参数则不可为负数
        if (fieldKey == "recruit_total") {
          const MaxSize = store.state.productPage.payload?.size_valid.value;
          if (MaxSize && val > MaxSize) {
            val = MaxSize;
            proxy.$message.warn("库存人数不能大于活动总人数", 5);
          }
        }
        let { canBeNegative } = props;
        if (!canBeNegative) {
          if (val < 0) {
            val = 0;
          }
        }
        updateKey(val * multiple);
      },
    });
    const updateKey = (val) => {
      if (fieldKey == "price_selling" || fieldKey == "price_original") {
        let price_celling = store.state.productPage.payload["price_selling"];
        let price_original = store.state.productPage.payload["price_original"];
        if (price_celling && price_original) {
          if (price_celling.value > price_original.value) {
            proxy.$message.warn("划线价格不能低于未划线价格", 5);
          }
        }
      }

      store.commit("productPage/setPayload", {
        [fieldKey]: {
          value: val,
        },
      });
    };
    const updateLength = function (l) {
      valueLength.value = l;
    };
    return {
      globalValue,
      fieldTitle,
      fieldKey,
      ins,
      pl,
      style,
    };
  },
};
</script>

