<template>
  <div style="position: relative">
    <div class="outer-box" :style="{ width: width + 'px' }">
      {{ getCurrentTime }}
      <img
        class="time-icon"
        @click="ShowSelect"
        src="./resource/Time.png"
        alt=""
      />
    </div>
    <div class="display-spa timeSelectBox" v-show="isShow">
      <div class="flex-box">
        <div class="item-list">
          <div
            @click="changeTime(item, 'hour')"
            class="item"
            v-for="(item, index) in hourList"
            :key="index"
          >
            {{ addZero(item) }}
          </div>
        </div>
        <div class="item-list">
          <div
            @click="changeTime(item, 'minute')"
            class="item"
            v-for="(item, index) in minuteList"
            :key="index"
          >
            {{ addZero(item) }}
          </div>
        </div>
      </div>
      <div class="submit-buttton" @click="submit">确 认</div>
    </div>
  </div>
</template>

<script>
import { reactive, toRaw } from "@vue/reactivity";
import { computed } from "@vue/runtime-core";
import { useStore } from "vuex";
import { ref } from "@vue/reactivity";
const store = useStore();
export default {
  name: "DayTimeSelectXXX",
  setup(props, context) {
    const generateArray = (start, end) => {
      return Array.from(new Array(end + 1).keys()).slice(start);
    };
    const hourList = generateArray(0, 23);
    const minuteList = generateArray(0, 59);
    return {
      hourList,
      minuteList,
    };
  },
  props: {
    value: String || Object,
    width: String,
  },
  mounted() {
    let box = Array.from(document.getElementsByClassName("timeSelectBox"));
    window.addEventListener("mousedown", (e) => {
      // 获取被点击的元素
      const clickedEl = e.target;
      // `el` 是你正在检测外部点击的元素
      if (
        box.some((item) => {
          return item.contains(clickedEl);
        })
      ) {
        // 单击“el”内部
      } else {
        // 在`el`之外点击
        this.isShow = false;
      }
    });
  },
  data() {
    return {
      hour: 0,
      minute: 0,
      first: true,
      isShow: false,
    };
  },
  computed: {
    getCurrentTime() {
      let value = !this.$props.width
        ? this.$store.state.productPage.payload[this.$props.value]?.value
        : this.$props?.value;
      if (this.first) {
        // 在模块1中也使用了这个时间选择器，在模块2也使用了时间选择器，但是他们的渲染逻辑不同，在模块1中没有传入width
        if (value) {
          if (value.indexOf("T") != -1) {
            return value.slice(-13, -8);
          }
          if (value.length < 6) {
            return value;
          }
          return this.timeTrans(value);
        }
        return "选择日期";
      }

      return `${this.hour} : ${this.minute}`;
    },
  },
  methods: {
    submit() {
      this.isShow = false;
    },
    addZero(num) {
      return Number(num) < 10 ? `0${num}` : num;
    },
    ShowSelect() {
      this.isShow = !this.isShow;
    },
    changeTime(value, type) {
      this.first = false;
      value = value < 10 ? `0${value}` : value;
      if (type == "hour") {
        this.hour = value;
        this.$emit("sendValue", `${this.hour} : ${this.minute}`);
        return;
      }
      this.minute = value;
      this.$emit("sendValue", `${this.hour} : ${this.minute}`);
      // 如果是模块1 ，需要更新store中的payload数据
      if (!this.$props.width) {
        const beforTime =
          this.$store.state.productPage.payload[this.$props.value]?.value;

        const newTime =
          beforTime.slice(0, 11) +
          `${this.hour}:${this.minute}` +
          beforTime.slice(16);
        console.log("📕", beforTime);
        console.log("📕", newTime);
        this.$store.commit("productPage/setPayload", {
          // 对应的payload中的全局表单的字段
          [this.$props.value]: {
            value: newTime,
          },
        });
      }
    },
    timeTrans(time, type) {
      let date = new Date(new Date(time).getTime() + 8 * 3600 * 1000);
      date = date.toJSON();
      if (type === 1) {
        date = date.substring(0, 10);
      } else {
        date = date.substring(10, 16).replace("T", " ");
      }
      return date;
    },
  },
};
</script>

