<template>
  <div class="label">报名年龄:</div>
  <div class="content">
    <integerInput :value="from" @valueChanged="fromChange" /> &nbsp; 岁 至
    &nbsp; <integerInput :value="to" @valueChanged="toChange" /> &nbsp; 岁
    <div class="pop" v-show="!ifAgreed && to > 65">
      <div class="img">风险提示</div>
      <div class="word">
        出行人超过65岁，组织者责任险可能不予承保，您应当对出行人员健康情况进行提前了解，建议其提供健康说明并督促其购买人身保险
      </div>
      <div class="submit button" @click="confirm">确定</div>
      <div class="button" @click="cancel">取消</div>
    </div>
    <div class="blacken" v-show="!ifAgreed && to > 65"></div>
  </div>
  <div class="clear"></div>
</template>

<script>
import { useStore } from "vuex";
import { toRefs } from "vue";
import { computed } from "@vue/runtime-core";
import IntegerInput from "@/pages/internal/views/providerTools/activityCreateForm/inputs/IntegerInput";

export default {
  name: "AgeLimit",
  props: {
    field: Object,
  },
  setup(props, context) {
    const store = useStore();
    const from = computed({
      get() {
        let entity = store.state.productPage.payload["age_min"];
        if (entity) return entity.value;
        return 1;
      },
      set(val) {
        let payload = {};
        payload["age_min"] = { value: val };
        store.commit("productPage/setPayload", payload);
      },
    });
    const to = computed({
      get() {
        let entity = store.state.productPage.payload["age_max"];
        if (entity) return entity.value;
        return 1;
      },
      set(val) {
        let payload = {};
        payload["age_max"] = { value: val };
        store.commit("productPage/setPayload", payload);
      },
    });
    return {
      from,
      to,
    };
  },
  components: {
    IntegerInput,
  },
  data() {
    return {
      ifAgreed: false,
    };
  },
  methods: {
    fromChange(val) {
      // console.log(val);
      this.from = val;
    },
    toChange(val) {
      // console.log(val);
      this.to = val;
    },
    confirm() {
      this.ifAgreed = true;
    },
    cancel() {
      this.to = 65;
    },
  },
};
</script>

