<template>
  <div class="label">{{ fieldTitle }}:</div>
  <div class="content">
    <div class="wrap dir-sechedule-sort" v-for="(item, i) in list" :key="i">
      <div class="date">
        <DatePicker2
          :value="list[item['num']]['date']"
          :num="item['num']"
          @valueChanged="valueChanged"
        ></DatePicker2>
        <!--                <input type="date" v-model="list[item['num']]['date']">-->
        <button v-show="item['num'] === list.length - 1" @click="addDay">
          添加日期
        </button>
        <button v-show="list.length !== 1" @click="deleteDay(item)">
          删除日期
        </button>
      </div>
      <div
        class="detail dir-sechedule-sort"
        v-clickoutside:dir-sechedule-sort="sortDate"
      >
        <div class="item" v-for="(date, i) in item['content']" :key="i">
          <!-- <div class="input"><input type="time" step="1800"  v-model="list[item['num']]['content'][date['num']]['from']">
                  <img src="../resource/Time.png" alt="">
                  </div>  -->
          <DayTimeSelect
            width="110"
            @sendValue="
              changeTimeForm($event, [item['num'], date['num'], 'from'])
            "
            :value="list[item['num']]['content'][date['num']]['from']"
          ></DayTimeSelect>
          ~
          <!-- <div class="input"> <input type="time" step="1800"  v-model="list[item['num']]['content'][date['num']]['to']">
                  <img src="../resource/Time.png" alt="">
                  </div> -->
          <DayTimeSelect
            width="110"
            @sendValue="
              changeTimeForm($event, [item['num'], date['num'], 'to'])
            "
            :value="list[item['num']]['content'][date['num']]['to']"
          ></DayTimeSelect>
          <input
            type="text"
            placeholder=" 请填写日程安排"
            v-model="list[item['num']]['content'][date['num']]['detail']"
          />
          &nbsp;
          <img src="../resource/delete.png" @click="deleteItem(item, date)" />
        </div>
        <div class="addTime" @click="addItem(item['num'])">添加一行</div>
      </div>
    </div>
  </div>
  <div class="clear"></div>
</template>

<script>
import { computed } from "@vue/runtime-core";
import { useStore } from "vuex";
import { toRefs } from "vue";
import DatePicker2 from "@/pages/internal/views/providerTools/activityCreateForm/inputs/DatePicker2";
import DayTimeSelect from "../../../inputs/DayTimeSelect.vue";
export default {
  name: "ScheduleComponent",
  props: {
    field: Object,
  },
  components: {
    DatePicker2,
    DayTimeSelect,
  },
  setup(props, context) {
    const store = useStore();
    const { fieldTitle } = toRefs(props.field);
    const globalValue = computed({
      get() {
        let entity = store.state.productPage.payload["daily_schedule"];
        if (entity) {
          return entity.value;
        }
        return [];
      },
      set(val) {
        store.commit("productPage/setPayload", {
          daily_schedule: { value: val },
        });
      },
    });
    return {
      globalValue,
      fieldTitle,
    };
  },
  created() {
    // let loadStore = this.loadStore;
    // this.internal = setInterval(function () {
    //     loadStore();
    // },1000);
    this.loadStore();
  },
  beforeUnmount() {
    // clearInterval(this.internal)
  },
  data() {
    return {
      value: "",
      list: [
        {
          num: 0, //第几个
          date: "", //date
          content: [
            {
              num: 0,
              from: "00:00",
              to: "12:00",
              detail: "",
            }, //具体日程
          ],
        },
      ],
      internal: 0,
    };
  },
  methods: {
    sortDate() {},
    changeTimeForm(e, ...args) {
      const [index, contentIndex, type] = Array.from(args)[0];

      this.list[index]["content"][contentIndex][type] = e;
      this.list.splice(0, 0);
    },
    valueChanged(val, num) {
      // this.value = val
      console.log(val, num);
      this.list[num]["date"] = val;
      this.list.splice(0, 0);
    },
    deleteDay(item) {
      if (this.list.length === 1) {
        return;
      }
      for (let i = item.num + 1; i < this.list.length; i++) {
        this.list[i].num -= 1;
      }
      this.list.splice(item.num, 1);
    },
    addDay() {
      this.list.push({
        num: this.list.length,
        date: "",
        content: [
          {
            num: 0,
            from: "",
            to: "",
            detail: "",
          },
        ],
      });
    },
    deleteItem(item, date) {
      if (this.list[item.num]["content"].length === 1) {
        return;
      }
      for (
        let i = date.num + 1;
        i < this.list[item.num]["content"].length;
        i++
      ) {
        this.list[item.num]["content"][i].num -= 1;
      }
      this.list[item.num]["content"].splice(date.num, 1);
      console.log(this.list);
    },
    addItem(index) {
      this.list[index]["content"].push({
        num: this.list[index]["content"].length,
        from: "",
        to: "",
        detail: "",
      });
    },
    loadStore() {
      let value = this.globalValue;
      if (value !== null && value.length !== 0) {
        let newList = [];
        for (let i = 0; i < value.length; i++) {
          let tmpDate = {};
          tmpDate.num = i;
          // console.log(value[i].date)
          tmpDate.date =
            value[i].date === "" ? "" : value[i].date.substr(0, 10);
          tmpDate.content = [];
          for (let j = 0; j < value[i].content.length; j++) {
            tmpDate.content[j] = {};
            tmpDate.content[j].num = j;
            tmpDate.content[j].from =
              value[i].content[j].from === ""
                ? "00:00"
                : value[i].content[j].from.substr(11, 5);
            tmpDate.content[j].to =
              value[i].content[j].to === ""
                ? "12:00"
                : value[i].content[j].to.substr(11, 5);
            tmpDate.content[j].detail = value[i].content[j].detail;
          }
          newList[i] = tmpDate;
        }
        if (JSON.stringify(this.list) !== JSON.stringify(newList))
          this.list = newList;
        // console.log(this.list)
      }
    },
  },
  watch: {
    list: {
      handler(val) {
        val = JSON.parse(JSON.stringify(val)).map((item) => {
          item.content = item.content.map((jtem) => {
            jtem.from = jtem.from.replaceAll(" ", "");
            jtem.to = jtem.to.replaceAll(" ", "");
            return jtem;
          });
          return item;
        });
        console.log(val);
        let temp = [];
        for (let i = 0; i < val.length; i++) {
          temp[i] = {};
          temp[i]["date"] =
            val[i]["date"] === "" ? "" : new Date(val[i]["date"]).toISOString();
          temp[i]["content"] = [];
          let content = val[i]["content"];
          for (let j = 0; j < content.length; j++) {
            temp[i]["content"][j] = {};
            let tmpDate = null;
            //设置detail
            temp[i]["content"][j]["detail"] = content[j]["detail"];
            //设置起始时间
            tmpDate =
              val[i]["date"] === "" ? new Date() : new Date(val[i]["date"]);
            const utchHours = content[j]["from"].substr(0, 2);
            tmpDate.setUTCHours(utchHours);
            tmpDate.setUTCMinutes(content[j]["from"].substr(3));
            temp[i]["content"][j]["from"] = tmpDate.toISOString();

            //设置结束时间
            tmpDate =
              val[i]["date"] === "" ? new Date() : new Date(val[i]["date"]);
            tmpDate.setUTCHours(content[j]["to"].substr(0, 2));
            tmpDate.setUTCMinutes(content[j]["to"].substr(3));
            temp[i]["content"][j]["to"] = tmpDate.toISOString();
          }
        }
        if (JSON.stringify(this.globalValue) !== JSON.stringify(temp))
          this.globalValue = temp;
      },
      deep: true,
    },
    globalValue: {
      deep: true,
      handler(val) {
        this.loadStore();
      },
    },
  },
};
</script>

