<template>
  <div class="label">师生比:</div>
  <div class="content">
    设置的师生比例是
    <IntegerInput :value="teacher" @valueChanged="teacherChange" /> :
    <IntegerInput :value="student" @valueChanged="studentChange" />
    &nbsp;&nbsp;<span style="color: grey">老师：学生</span>
  </div>
  <div class="clear"></div>
</template>

<script>
import { useStore } from "vuex";
import { toRefs } from "vue";
import { computed } from "@vue/runtime-core";
import IntegerInput from "@/pages/internal/views/providerTools/activityCreateForm/inputs/IntegerInput";

export default {
  name: "TeacherRatio",
  setup(props, context) {
    const store = useStore();

    store.commit("productPage/setPayload", {
      teacher_student: { value: [1, 1] },
    });

    const teacher = computed({
      get() {
        let entity = store.state.productPage.payload["teacher_student"];
        if (entity) return entity.value[0];
        return 1;
      },
      set(val) {
        console.log(val);
        let entity = store.state.productPage.payload["teacher_student"];
        let payload = {};
        payload["teacher_student"] = {
          value: [val, entity ? entity.value[1] : 1],
        };
        store.commit("productPage/setPayload", payload);
      },
    });
    const student = computed({
      get() {
        let entity = store.state.productPage.payload["teacher_student"];
        if (entity) return entity.value[1];
        return 1;
      },
      set(val) {
        console.log(val);
        let entity = store.state.productPage.payload["teacher_student"];
        let payload = {};
        payload["teacher_student"] = {
          value: [entity ? entity.value[0] : 1, val],
        };
        store.commit("productPage/setPayload", payload);
      },
    });
    return {
      teacher,
      student,
    };
  },
  components: {
    IntegerInput,
  },
  methods: {
    teacherChange(val) {
      // console.log(val);
      this.teacher = val;
    },
    studentChange(val) {
      // console.log(val);
      this.student = val;
    },
  },
};
</script>

<style scoped>
select {
  width: 150px;
  height: 25px;
}
</style>
