<template>
  <div class="sel-days">
    <div class="select-days">{{ days }}<span class="text">天</span></div>
    <slot></slot>
  </div>
</template>

<script>
import { toRaw } from '@vue/reactivity'
import FieldStructure from './FieldStructure.vue'
import { useStore } from 'vuex'
import { computed } from '@vue/runtime-core'
import Toast from '/src/pages/internal/components/common/toast/Toast.vue'

export default {
  components: {
    FieldStructure
  },
  props: {
    field: Object
  },
  setup(props, context) {
    console.log(context)
    const store = useStore()
    const { fieldTitle, fieldKey } = toRaw(props.field)

    const globalValue = computed({
      get() {
        let value = store.state.productPage.payload[fieldKey]

        if (!value) {
          return ''
        }
        return value.value
      },
      set(val) {
        updateKey(val)
      }
    })
    const days = computed({
      get() {
        let startdate = store.state.productPage.payload['activity_start_time']
        let enddate = store.state.productPage.payload['activity_end_time']
        if (startdate && enddate) {
          startdate = new Date(startdate.value)
          enddate = new Date(enddate.value)
        } else {
          startdate = enddate = new Date()
        }
        let days = Math.ceil((enddate - startdate) / 60 / 60 / 24 / 1000)
        store.commit('productPage/setPayload', {
          [fieldKey]: {
            value: days
          }
        })

        return days + 1
      },
      set(val) {
        let product_launching_time =
          store.state.productPage.payload['product_launching_time']
        if (
          product_launching_time &&
          new Date(product_launching_time) > new Date(val)
        ) {
          Toast.warn('1')
          return
        }
        updateKey(val)
      }
    })
    const updateKey = val => {
      store.commit('productPage/setPayload', {
        [fieldKey]: {
          value: val
        }
      })
    }

    return {
      fieldTitle,
      globalValue,
      days
    }
  },
  methods: {
    warn(text) {
      this.$toast.warn(text)
    }
  },
  data() {
    return {}
  }
}
</script>

